module.exports = {"docs":[{"order":2,"title":"更新日志","__content":"\n\n`Yep-React` 严格遵循 [Semantic Versioning 2.0.0](http://semver.org/lang/zh-CN/) 语义化版本规范。\n\n#### 发布周期\n\n* 修订版本号：每周末会进行日常 bugfix 更新。（如果有紧急的 bugfix，则任何时候都可发布）\n* 次版本号：每月发布一个带有新特性的向下兼容的版本。\n* 主版本号：含有破坏性更新和新特性，不在发布周期内。\n\n---\n\n### 0.2.22\n\n`2018-11-19`\n\n* **Feature**\n\n  * 🌟 优化构建工作流，使用 yep-tools\n\n- **Bug Fix**\n\n  * 🐞 AreaPicker 组件初始化默认选择时调用接口未传 index 的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.21\n\n`2018-11-19`\n\n* **Feature**\n\n  * 🌟 AreaPicker 组件新增支持初始化时选中默认的地区\n\n- **Bug Fix**\n\n  * 🐞 修改文档中的错误\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.20\n\n`2018-11-08`\n\n* **Feature**\n\n  * 🌟 组件库编译时使用 babel-runtime helper\n\n- **Bug Fix**\n\n  * 🐞 SwipeAction left 或 right 为空时自动切换 Swipe 状态时丢失 cover 导致的元素隐藏\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.19\n\n`2018-11-08`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 SwipeAction left 或 right 动态改变时，重新计算宽度\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.18\n\n`2018-11-07`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 react-transition-group 依赖丢失\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.17\n\n`2018-11-07`\n\n* **Feature**\n\n  * 🌟 AreaPicker 支持自定义数据格式、滑动触发切换安全距离\n\n- **Bug Fix**\n\n  * 🐞 SwipeAction 当 right 和 left 为空时的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.16\n\n`2018-11-06`\n\n* **Feature**\n\n  * 🌟 新增主站地区选择组件 AreaPicker\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.15\n\n`2018-10-30`\n\n* **Feature**\n\n  * 🌟 SwipeAction 组件新增 onMovingDistance 回调，实时返回滑动的距离，左滑为-，右滑为+\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.14\n\n`2018-10-18`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 IndexedList 右侧索引滑动时左侧页面抖动(Ios 下)\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.13\n\n`2018-10-16`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 IndexedList 右侧索引滑动时左侧页面抖动\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.12\n\n`2018-10-16`\n\n* **Feature**\n\n  * 🌟 新增 SearchBar 组件\n\n- **Bug Fix**\n\n  * 🐞 Checkbox 组件的大小\n\n- **Enhancement**\n\n  * Message 组件提示图标可定制\n\n### 0.2.11\n\n`2018-10-12`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * IndexedList 组件新增 keyLabel 来获取唯一的 key\n\n### 0.2.10\n\n`2018-10-11`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 Dialog 的 confirm 组件更多参数的传参方式，第四个参数 restProps 为 object 类型，支持 Dialog 组件的其余属性\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.9\n\n`2018-10-09`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修改 IndexedList renderRow 回调参数，之前是 label，现在修改为 label 所在的一行数据 object\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.8\n\n`2018-10-08`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 Switch 组件外部 currentStatus 改变时不能改变 Switch 组件内部状态的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.7\n\n`2018-09-27`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 `Tabs` 组件 无法右滑切换的 bug\n\n- **Enhancement**\n\n  * 手势组件独立出去使用 ts 重构，直接引用@jdcfe/yep-gesture\n\n### 0.2.6\n\n`2018-09-27`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 `Tabs` 组件 onChange 回调未触发的 bug，回调参数为 index\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.5\n\n`2018-09-11`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 重构 `Dropdown` 组件，修改 props\n  * 🐞 修复 `AutoComplete` 组件\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.4\n\n`2018-09-11`\n\n* **Feature**\n\n  * 🌟 Dialog 的 confirm 组件提供更多参数\n\n- **Bug Fix**\n\n  * 🐞 修复 Dropdown 组件的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.3\n\n`2018-08-29`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 Button 组件 overflow：hidden 导致 border 不可见的问题\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.2\n\n`2018-08-14`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 InputItem 的 Placeholder 上下不居中的问题\n\n- **Enhancement**\n\n  * 无\n\n### 0.2.1\n\n`2018-08-08`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复在 ios 下 InputItem editable 为 false 的情况下，onClick 点击会触发 focus 导致弹出键盘的问题\n\n- **Enhancement**\n\n  * 新增组件库字体\n\n### 0.2.0\n\n`2018-07-26`\n\n经过 18 个小版本迭代，发布 0.2.0 版本。\n\n* **Feature**\n\n  * 🌟 重新设计全部组件规范，支持自定义主题。组件按需加载支持引入 css 和 scss 源文件\n  * 🌟 新增 Tips 组件\n  * 🌟 新增 AutoComplete 组件\n  * 🌟 新增 Dropdown 组件\n  * 🌟 新增 InputItem 组件\n  * 🌟 新增 Carousel 组件\n  * 🌟 新增 Rate 组件\n  * 🌟 新增 DatePicker 组件\n  * 🌟 新增 PickerVIew 组件\n  * 🌟 Picker 组件支持多列数据，支持级联选择\n  * 🌟 Placeholder 组件更名为 Skeleton，用来配置页面骨架组件\n\n- **Bug Fix**\n\n  * 🐞 修改部分 scss 变量可覆盖\n  * 🐞 重构 Button 组件的 border 实现方式\n\n- **Enhancement**\n\n  * `List.Item` 组件右侧 图标支持自定义，当 `icon`为 string，需要符合 Icon type，如果是自定义组件，则直接渲染\n  * `IndexedList` 组件新增 `activeBar` 属性，支持外界影响 bar 的 active 状态\n  * `TabBar` 支持自定义 `badge` `dot` `hidden` 属性\n  * `Button` 支持 `type=ghost`类型\n  * `List` 新增 `Brief`次级内容\n  * 引入 FlowType 静态检查\n\n### 0.1.18\n\n`2018-07-24`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 规范 Dialog confirm 组件样式\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.17\n\n`2018-07-20`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 Picker 及 DatePicker 组件未滑动点击确定时的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.16\n\n`2018-07-18`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * Swiper 组件支持无限滚动\n\n### 0.1.15\n\n`2018-07-17`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * List.Item 组件右侧 图标支持自定义，当 icon 为 string，需要符合 Icon type，如果是自定义组件，则直接渲染\n\n### 0.1.14\n\n`2018-07-17`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 DatePicker 关闭时未正确返回数据的问题\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.13\n\n`2018-07-17`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 hairline-bottom 的问题\n  * 🐞 修复 InputItem 字体大小为 32px\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.12\n\n`2018-07-16`\n\n* **Feature**\n\n  * 🌟 新增 InputItem、DatePicker、PickerVIew 组件\n\n- **Bug Fix**\n\n  * 🐞\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.11\n\n`2018-07-10`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复部分组件按需加载依赖错误\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.10\n\n`2018-07-09`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 因为 prettier 导致 1PX 为 1px 的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.9\n\n`2018-07-06`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 重构 Button 组件的 border\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.8\n\n`2018-07-05`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 List 组件依赖 Icon 组件样式\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.7\n\n`2018-06-29`\n\n* **Feature**\n\n  * 🌟 新增 Rate 组件\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.6\n\n`2018-06-26`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 修复 Carousel 组件数据只有一条时回弹的 bug\n\n- **Enhancement**\n\n  * 无\n\n### 0.1.5\n\n`2018-06-21`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 📝 Carousel 组件新增 `isBounces` 属性支持,支持是否允许回弹效果\n\n### 0.1.4\n\n`2018-06-20`\n\n* **Feature**\n\n  * 🌟 无\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 📝 IndexedList 组件新增 activeBar 属性，支持外界影响 bar 的 active 状态\n\n### 0.1.3\n\n`2018-06-20`\n\n* **Feature**\n\n  * 🌟 新增 Carousel 组件\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 📝 无\n\n### 0.1.2\n\n`2018-06-14`\n\n* **Feature**\n\n  * 🌟 新增 AutoComplete 组件\n  * 🌟 新增 Dropdown 组件\n  * 🌟 新增 Input 组件\n  * 🌟 样式按需加载提供 css 的引入方式\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 📝 代码引入 FlowType 静态检查及相应静态检查问题修复\n\n### 0.1.1\n\n`2018-06-01`\n\n* **Feature**\n\n  * 🌟 根据设计稿重新设计大部分组件规范\n  * 🌟 支持组件[自定义主题样式](https://jdf2e.github.io/yep-react/#/doc/customize-theme)\n  * 🌟 新增 Tips 组件\n  * 🌟 `TabBar` 支持自定义 `badge` `dot` `hidden` 属性\n  * 🌟 `Button` 支持 `type=ghost`类型\n  * 🌟 `List` 新增 `Brief`次级内容\n\n- **Bug Fix**\n\n  * 🐞 无\n\n- **Enhancement**\n\n  * 📝 更新 `TabBar` 使用文档\n  * 📝 更新 `Button` 文档\n  * 📝 更新 `List` 使用文档\n\n### 0.1.0\n\n`2018-05-25`\n\n* 🌟🌟🌟 Release 0.1.0\n","id":"CHANGELOG"},{"order":3,"title":"定制主题","__content":"\n\nYep-React 设计规范上支持一定程度的样式定制，以满足业务和品牌上多样化的视觉需求，包括但不限于主色、圆角、边框和部分组件的视觉定制。\n\n## 样式变量\n\nYep-React 的样式使用了 [scss](http://sass-lang.com/) 作为开发语言，并定义了一系列全局/组件的样式变量，你可以根据需求进行相应调整。\n\n* [默认样式变量](https://github.com/jdf2e/yep-react/blob/master/src/style/variables.scss)\n\n## 定制方式\n\n我们使用 [options data](http://sass-lang.com/documentation/file.SASS_REFERENCE.html#variable_defaults_default) 的方式来覆盖变量。\n\n### 1) data 属性 字符串\n\n* 首先，你的项目里需要包含如下依赖 `babel-plugin-import node-sass sass-loader style-loader css-loader` 。\n\n```bash\n  npm install --save-dev babel-plugin-import babel-plugin-import node-sass sass-loader style-loader css-loader\n```\n\n* 配置 babel-plugin-import 确保加载 Yep-React scss 文件\n\n```javascript\n{\n    ...\n    \"plugins\": [\n        [\"import\", {\"libraryName\": \"@jdcfe/yep-react\", \"style\": true}],\n        ...\n    ]\n}\n```\n\n* 在 `webpack` 配置文件中添加 scss 文件的 sass-loader 处理，在 options 选项里面将包含所有我们想要修改的主题样式。[全部主题样式参考这里](https://github.com/jdf2e/yep-react/blob/master/src/style/variables.scss)\n\n```javascript\n  {\n    test: /\\.scss$/,\n    use: [\n        ...,\n        {\n            loader: require.resolve('sass-loader'),\n            options: {\n                data: '$brand-primary:#c23e25;',//定制修改品牌首选色\n\n            }\n        }\n    ],\n},\n```\n\n* 最后，在你的 webpack （版本 3.0+） 配置文件里，添加如上配置，之后运行你的 `npm start`。\n\n> 不同版本的 webpack loader 配置方法有差异，请查看 webpack 官方文档\n\n### 2) theme.json\n\n用 json 文件进行变量覆盖。\n\n创建一个单独的 `theme.json` 文件如下，再引入这个文件。\n\n```json\n[\"$brand-primary:#2A83E1;\", \"$button-height:100px;\"]\n```\n\n```javascript\nconst theme = require('theme.json')\n\n{\n    test: /\\.scss$/,\n    use: [\n        ...,\n        {\n            loader: require.resolve('sass-loader'),\n            options: {\n                data: theme.join(' '),\n            }\n        }\n    ],\n},\n```\n","id":"customize-theme"},{"order":1,"title":"设计原则","__content":"\n\n### 基于LRC文档和组件规范\n\nYep-React是从LRC基础组件库fork出的M端通用react组件库，使用了LRC的文档生成模块，待接入的是组件测试流程。\n\n### 自有的Yep-React组件规范和流程\n\nYep-React除了继承了LRC基础规范，也加了自己的添加规范化组件脚本和样式按需引入插件，实现了一键生成组件模板文件和用户按需引入组件对应样式的功能。\n\n### 符合京东app视觉、交互规范\n符合京东app视觉、交互规范，满足在app内通用流程组件的开箱即用。","id":"design-principle"},{"order":0,"title":"快速上手","__content":"\n\n如果是在[butler](http://butler.jd.com/#/)中使用，请直接访问[在 butler 中使用](#/doc/use-with-butler).\n\n开始使用前请先配置[京东内部 npm 源](http://npm.m.jd.com/)。\n\n### 安装\n\n使用 [`yarn`](https://yarnpkg.com/en/) 安装：\n\n```bash\nyarn add @jdcfe/yep-react\n```\n\n或是使用 [`npm`](https://www.npmjs.com/)：\n\n```bash\nnpm install --save @jdcfe/yep-react\n```\n\n### 使用\n\n入口页面 (html 或 模板) 相关设置：\n\n> 屏幕适配(更多参考 [wiki](https://github.com/jdf2e/yep-react/wikis/%E7%BB%84%E4%BB%B6%E5%BA%93%E5%9F%BA%E4%BA%8E750%E8%AE%BE%E8%AE%A1%E7%A8%BF%E5%BC%80%E5%8F%91%EF%BC%8C%E5%B1%8F%E5%B9%95%E9%80%82%E9%85%8D))\n\n```html\n<!DOCTYPE html>\n<html>\n<head>\n  <!-- set `maximum-scale` for some compatibility issues -->\n  <meta name=\"viewport\" content=\"width=device-width, initial-scale=1, maximum-scale=1, minimum-scale=1, user-scalable=no\" />\n</head>\n<body>\n</body>\n</html>\n```\n\n组件使用实例：\n\n```jsx\nimport {Button} from '@jdcfe/yep-react';\nReactDOM.render(<Button>Start</Button>, mountNode);\n```\n\n引入样式：\n\n```jsx\nimport '@jdcfe/yep-react/dist/yep-react.css';\n```\n\n##### 按需加载\n\n**注意：强烈推荐使用。**\n\n下面两种方式都可以**只加载**用到的组件，选择其中一种方式即可。\n\n* 使用 [babel-plugin-import](https://github.com/ant-design/babel-plugin-import)（推荐）。\n\n  ```javascript\n  // babel 6  .babelrc or babel-loader option\n  {\n    \"plugins\": [\n      [\"import\", { libraryName: \"@jdcfe/yep-react\", style: \"css\" }] //`style: \"css\"`会加载css文件, `style: true` 会加载 scss 文件,则必须引入sass-loader\n    ]\n  }\n  ```\n\n  ```javascript\n    // babel 7  .babelrc or babel-loader option\n    {\n      \"plugins\": [\n        [\"import\", { libraryName: \"@jdcfe/yep-react\", style: \"css\" },\"@jdcfe/yep-react\"] //`style: \"css\"`会加载css文件, `style: true` 会加载 scss 文件,则必须引入sass-loader\n      ]\n    }\n  ```\n\n  然后只需从 @jdcfe/yep-react 引入模块即可，无需单独引入样式。\n\n  ```jsx\n  // babel-plugin-import 会帮助你加载 JS 和 SCSS\n  import {Button} from '@jdcfe/yep-react';\n  ```\n\n* 手动引入\n\n  ```jsx\n  import Button from '@jdcfe/yep-react/lib/button'; // 加载 JS\n  import '@jdcfe/yep-react/lib/button/style'; // 加载 SCSS\n  ```\n","id":"get-started"},{"order":4,"title":"在butler中使用","__content":"\n\n[butler](http://butler.jd.com/#/) A simple build tool for web developer 。\n\n---\n\n## 安装和初始化\n\n安装前请先配置[京东私有 npm 源](http://npm.m.jd.com/)。\n\n```bash\nnpm config set registry http://registry.m.jd.com\n```\n\n或是使用 [nrm](https://github.com/Pana/nrm):\n\n```bash\nnrm add jd http://registry.m.jd.com\nnrm use jd\n```\n\n然后执行以下命令安装。如果已安装，请确认@jdcfe/butler 最低 0.1.5 版本\n\n```bash\nnpm install -g @jdcfe/butler\n```\n\n初始化新项目\n\n```bash\nbutler init butler-yep-react-demo\n// or\nbr init butler-yep-react-demo\n```\n\n工具会自动初始化一个脚手架并安装 React 项目的各种必要依赖。\n\n然后我们进入项目并启动。\n\n```bash\n$ cd butler-yep-react-demo\n$ butler start\n```\n\n此时使用浏览器访问 http://127.0.0.1:3000/app.html ，看到界面就算成功了。\n\n## 引入 Yep-React\n\n现在从 yarn 或 npm 安装并引入 Yep-React。\n\n```bash\n$ yarn add @jdcfe/yep-react\n```\n\n修改 `src/js/pages/app.jsx`，引入 Yep-React 的按钮组件。\n\n```jsx\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport '../../css/app.scss';\nimport Button from '@jdcfe/yep-react/lib/button';\nimport '@jdcfe/yep-react/lib/button/style';\n\nReactDOM.render(\n  <div className=\"demo-template\">\n    <Button type={'primary'}>Yep-react</Button>\n  </div>,\n  document.getElementById('app')\n);\n\nif (module.hot) {\n  module.hot.accept();\n}\n```\n\n根目录新增 `postcss.config.js`文件\n\n```bash\n$ yarn add -D  postcss-pxtorem\n```\n\n```js\nconst pxtorem = require('postcss-pxtorem');\n\nmodule.exports = {\n  plugins: [\n    pxtorem({\n      rootValue: 100,\n      propWhiteList: [],\n    }),\n  ],\n};\n```\n\n修改 `src/html/app.html`,在 head 中添加如下\n\n```css\n<style>\n  @media screen and (min-width: 320px) { /*no*/\n    html {\n      font-size: calc(42.67px + 2 * (100vw - 320px) / 55); /*no*/\n    }\n  }\n\n  @media screen and (min-width: 375px) {\n    html {\n      font-size: calc(50px + 2 * (100vw - 375px) / 39); /*no*/\n    }\n  }\n\n  @media screen and (min-width: 414px) {\n    html {\n      font-size: calc(55.2px + 4 * (100vw - 414px) / 186); /*no*/\n    }\n  }\n\n  @media screen and (min-width: 600px) {\n    html {\n      font-size: calc(80px + 4 * (100vw - 600px) / 150); /*no*/\n    }\n  }\n\n  @media screen and (min-width: 750px) {\n    html {\n      font-size: calc(100px + 6 * (100vw - 1000px) / 1000); /*no*/\n    }\n  }\n\n</style>\n```\n\n好了，现在你应该能看到页面上已经有了 Yep-React 的按钮组件，接下来就可以继续选用其他组件开发应用了。其他开发流程你可以参考 butler [官方文档](http://butler.jd.com/#/)。\n\n## 高级配置\n\n我们现在已经把组件成功运行起来了，但是在实际开发过程中还有很多问题，例如上面的例子实际上加载了全部的 Yep-React 组件的样式（对前端性能是个隐患）。\n\n此时我们需要对 butler 的默认配置进行自定义。\n\n### 使用 babel-plugin-import\n\n[babel-plugin-import](https://github.com/ant-design/babel-plugin-import) 是一个用于按需加载组件代码和样式的 babel 插件，现在我们尝试安装它并在项目根目录新增 `.babelrc` 文件。\n\n```bash\n$ yarn add -D babel-plugin-import\n```\n\n```.babelrc\n{\n  \"plugins\": [\n    [\"import\", { \"libraryName\": \"@jdcfe/yep-react\", \"style\": true }]\n  ]\n}\n```\n\n然后移除前面在 `src/js/pages/app.jsx` 里添加的 `@jdcfe/yep-react/lib/button/style` Button 组件样式代码，并且按下面的格式引入模块。\n\n```diff\n  // src/App.js\n  import React from 'react';\n  import ReactDOM from 'react-dom';\n  import '../../css/app.scss';\n- import Button from '@jdcfe/yep-react/lib/button';\n- import '@jdcfe/yep-react/lib/button/style';\n+ import { Button } from '@jdcfe/yep-react';\n\n  ReactDOM.render(\n    <div className=\"demo-template\">\n      <Button type={'primary'}>Yep-react</Button>\n    </div>,\n    document.getElementById('app'),\n  );\n\n  if (module.hot) {\n    module.hot.accept();\n  }\n```\n\n最后重启 `butler start` 访问页面，Yep-React 组件的 js 和 css 代码都会按需加载，你在控制台也不会看到这样的[警告信息](https://zos.alipayobjects.com/rmsportal/vgcHJRVZFmPjAawwVoXK.png)。关于按需加载的原理和其他方式可以阅读[这里](#/docs/react/getting-started#按需加载)。\n\n### 自定义主题\n\n按照 [配置主题](#/doc/customize-theme) 的要求，自定义主题需要用到 scss 变量覆盖功能。\n\n根目录新增`sassloader.option.config.js`\n\n定制主题较少时\n\n```js\nmodule.exports = {\n  option: {\n    data: '$brand-primary:#2A83E1;',\n  },\n};\n```\n\n或者定制主题较多时\n\n```js\nconst theme = ['$brand-primary:#2A83E1;', '$button-height:100px;'];\n\nmodule.exports = {\n  option: {\n    data: theme.join(' '),\n  },\n};\n```\n\n这里利用了 [scss-loader](http://sass-lang.com/documentation/file.SASS_REFERENCE.html#variable_defaults_default) 的 `options data` 来进行主题配置，变量和其他配置方式可以参考 [配置主题](#/doc/customize-theme) 文档。\n\n修改后重启 `butler start`，如果看到一个蓝色的按钮就说明配置成功了。\n\n在线示例\n\n* [butler-yep-react](http://git.jd.com/zhaohongyang1/butler-yep-react)\n","id":"use-with-butler"}],"components":{"action-sheet":{"name":"action-sheet","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport {ActionSheet, Button, WhiteSpace} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  constructor() {\n    super();\n    this.handleToggleShow = this.handleToggleShow.bind(this);\n    this.handleToggleSlideDownShow = this.handleToggleSlideDownShow.bind(this);\n    this.handleToggleSlideRightShow = this.handleToggleSlideRightShow.bind(this);\n    this.state = {\n      show: false,\n    };\n  }\n\n  handleToggleShow() {\n    this.setState({\n      show: !this.state.show,\n    });\n  }\n\n  handleToggleSlideDownShow() {\n    this.setState({\n      slideDownShow: !this.state.slideDownShow,\n    });\n  }\n\n  handleToggleSlideRightShow() {\n    this.setState({\n      slideRightShow: !this.state.slideRightShow,\n    });\n  }\n\n  render() {\n    return (\n      <div>\n        <Button type=\"primary\" onClick={this.handleToggleShow}>\n          显示\n        </Button>\n        <WhiteSpace />\n        <Button onClick={this.handleToggleSlideDownShow}>显示取消按钮</Button>\n        <WhiteSpace />\n        <Button type=\"primary\" onClick={this.handleToggleSlideRightShow}>\n          婚姻状况\n        </Button>\n\n        <ActionSheet show={this.state.show} onCancel={this.handleToggleShow} data={['未婚', '已婚', '离异']} />\n\n        <ActionSheet\n          show={this.state.slideDownShow}\n          onCancel={this.handleToggleSlideDownShow}\n          hasCancel\n          space\n          data={['未婚', '已婚', '离异']}\n          active={1}\n        />\n        <ActionSheet\n          show={this.state.slideRightShow}\n          onCancel={this.handleToggleSlideRightShow}\n          hasCancel\n          space\n          title=\"婚姻状况\"\n          data={['未婚', '已婚', '离异']}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"动作面板","__content":"\n\n<DEMO>\n\n| 参数      | 说明                       | 类型               | 默认值           |\n| --------- | -------------------------- | ------------------ | ---------------- |\n| show      | 是否显示                   | bool               | false            |\n| onCancel  | 关闭事件回调               | func               | () =>{}          |\n| itemClick | 点击事件回调               | func               | () =>{}          |\n| prefixCls | 组件 class 前缀            | string             | Yep-action-sheet |\n| style     | 组件样式                   | object             | {}               |\n| title     | 是否拥有 title             | string             |\n| space     | 是否显示取消按钮上面的空白 | boolean            | false            |\n| hasCancel | 是否显示取消按钮           | boolean            | false            |\n| data      | 选项数据                   | array              | []               |\n| active    | 选中的 item                | number 或者 string | -                |\n| className | 额外要添加的 CSS 类名      | string             | -                |\n"},"area-picker":{"name":"area-picker","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Button, AreaPicker} from '@jdcfe/yep-react';\nimport jsonp from 'jsonp';\nconst province = [\n  {id: 1, name: '北京'},\n  {id: 2, name: '上海'},\n  {id: 3, name: '天津'},\n  {id: 4, name: '重庆'},\n  {id: 5, name: '河北'},\n  {id: 6, name: '山西'},\n  {id: 7, name: '河南'},\n  {id: 8, name: '辽宁'},\n  {id: 9, name: '吉林'},\n  {id: 10, name: '黑龙江'},\n  {id: 11, name: '内蒙古'},\n  {id: 12, name: '江苏'},\n  {id: 13, name: '山东'},\n  {id: 14, name: '安徽'},\n  {id: 15, name: '浙江'},\n  {id: 16, name: '福建'},\n  {id: 17, name: '湖北'},\n  {id: 18, name: '湖南'},\n  {id: 19, name: '广东'},\n  {id: 20, name: '广西'},\n  {id: 21, name: '江西'},\n  {id: 22, name: '四川'},\n  {id: 23, name: '海南'},\n  {id: 24, name: '贵州'},\n  {id: 25, name: '云南'},\n  {id: 26, name: '西藏'},\n  {id: 27, name: '陕西'},\n  {id: 28, name: '甘肃'},\n  {id: 29, name: '青海'},\n  {id: 30, name: '宁夏'},\n  {id: 31, name: '新疆'},\n  {id: 32, name: '台湾'},\n  {id: 84, name: '钓鱼岛'},\n  {id: 52993, name: '港澳'},\n  {id: 53283, name: '海外'},\n];\n\nconst getArea = params => {\n  return new Promise((resolve, reject) => {\n    jsonp(`//d.jd.com/area/get?fid=${params}&t=${Math.floor(Math.random() * 10000)}`, null, (err, ret) => {\n      if (err) {\n        reject(err);\n      } else {\n        resolve(ret);\n      }\n    });\n  });\n};\n\nclass Demo extends React.PureComponent {\n  state = {\n    show: false,\n    data: province,\n  };\n\n  onCancel = () => {\n    this.setState({show: false});\n  };\n\n  onShow = () => {\n    this.setState({show: true});\n  };\n\n  onOk = (values, index) => {\n    console.log(values);\n    this.setState({show: false});\n  };\n\n  render() {\n    return (\n      <div>\n        <Button onClick={this.onShow} type={'ghost'}>\n          点我\n        </Button>\n        <AreaPicker\n          chooseLabel={'12321'}\n          show={this.state.show}\n          onCancel={this.onCancel}\n          initialData={this.state.data}\n          onOk={this.onOk}\n          fetchAction={(city, index) => getArea(city.id)}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"默认选中","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Button, AreaPicker} from '@jdcfe/yep-react';\nimport jsonp from 'jsonp';\nconst province = [\n  {id: 1, name: '北京'},\n  {id: 2, name: '上海'},\n  {id: 3, name: '天津'},\n  {id: 4, name: '重庆'},\n  {id: 5, name: '河北'},\n  {id: 6, name: '山西'},\n  {id: 7, name: '河南'},\n  {id: 8, name: '辽宁'},\n  {id: 9, name: '吉林'},\n  {id: 10, name: '黑龙江'},\n  {id: 11, name: '内蒙古'},\n  {id: 12, name: '江苏'},\n  {id: 13, name: '山东'},\n  {id: 14, name: '安徽'},\n  {id: 15, name: '浙江'},\n  {id: 16, name: '福建'},\n  {id: 17, name: '湖北'},\n  {id: 18, name: '湖南'},\n  {id: 19, name: '广东'},\n  {id: 20, name: '广西'},\n  {id: 21, name: '江西'},\n  {id: 22, name: '四川'},\n  {id: 23, name: '海南'},\n  {id: 24, name: '贵州'},\n  {id: 25, name: '云南'},\n  {id: 26, name: '西藏'},\n  {id: 27, name: '陕西'},\n  {id: 28, name: '甘肃'},\n  {id: 29, name: '青海'},\n  {id: 30, name: '宁夏'},\n  {id: 31, name: '新疆'},\n  {id: 32, name: '台湾'},\n  {id: 84, name: '钓鱼岛'},\n  {id: 52993, name: '港澳'},\n  {id: 53283, name: '海外'},\n];\n\nconst getArea = params => {\n  return new Promise((resolve, reject) => {\n    jsonp(`//d.jd.com/area/get?fid=${params}&t=${Math.floor(Math.random() * 10000)}`, null, (err, ret) => {\n      if (err) {\n        reject(err);\n      } else {\n        resolve(ret);\n      }\n    });\n  });\n};\n\nclass Demo extends React.PureComponent {\n  state = {\n    show: false,\n    data: province,\n  };\n\n  onCancel = () => {\n    this.setState({show: false});\n  };\n\n  onShow = () => {\n    this.setState({show: true});\n  };\n\n  onOk = (values, index) => {\n    console.log(values);\n    this.setState({show: false});\n    alert(JSON.stringify([values]));\n  };\n\n  render() {\n    return (\n      <div>\n        <Button onClick={this.onShow} type={'ghost'}>\n          点我\n        </Button>\n        <AreaPicker\n          chooseLabel={'12321'}\n          show={this.state.show}\n          onCancel={this.onCancel}\n          initialData={this.state.data}\n          onOk={this.onOk}\n          fetchAction={(city, index) => getArea(city.id)}\n          selected={[{id: 1, name: '北京'}, {name: '东城区', id: 2802}, {id: 54748, name: '建国门街道'}]}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"主站地区选择组件","__content":"\n\n主站地区选择组件\n\n<DEMO>\n\n| 参数                | 说明                                         | 类型                                                                                        | 默认值                     | 必填  |\n| ------------------- | -------------------------------------------- | ------------------------------------------------------------------------------------------- | -------------------------- | ----- |\n| prefixCls           | 组件 class 前缀                              | string                                                                                      | Yep-area-picker            | false |\n| style               | 组件样式                                     | object                                                                                      | {}                         | false |\n| className           | 组件额外样式                                 | string                                                                                      | -                          | false |\n| maskCloseable       | 点击遮罩层是否收起                           | boolean                                                                                     | false                      | false |\n| distanceToChangeTab | 左右滑动触发切换的安全距离                   | number                                                                                      | 10                         | false |\n| title               | 顶部标题                                     | string                                                                                      | `配送至`                   | false |\n| initialData         | 顶层数据                                     | `array[{id:number,name:string}]`                                                            | -                          | true  |\n| onOk                | 选中最后一级数据时执行的回调                 | func                                                                                        | -                          | true  |\n| onCancel            | 点击关闭按钮时执行的回调                     | func                                                                                        | -                          | true  |\n| fetchAction         | 获取二三四级数据接口的回调，必须返回 Promise | `func (city:{id,name,...},index:选中数据的层级(0,1,2)) => promise`                          | -                          | true  |\n| keyExtractor        | 用来获取唯一的 key                           | `func (item: object, index: number) => number;`                                             | (item, index) => item.id   | false |\n| nameExtractor       | 用来获取地区的显示                           | `func (item: object) => string;`                                                            | (item, index) => item.name | false |\n| chooseLabel         | 未选择时 tab 显示的文案                      | string                                                                                      | `请选择`                   | false |\n| selected            | 默认选中的地区                               | `array[{id:number,name:string}]`如果不是 id 和 name,必须配置 `keyExtractor` `nameExtractor` | []                         | false |\n"},"auto-complete":{"name":"auto-complete","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {AutoComplete} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <AutoComplete\n          placeholder=\"输入关键词\"\n          size={'lg'}\n          clear={true}\n          onChange={v => {\n            console.log(v);\n          }}\n          source={['愿得一人心', '后宫佳丽三千人']}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"自动完成","__content":"\n\n自动完成\n\n<DEMO>\n\n| 参数         | 说明                     | 类型             | 默认值            |\n| ------------ | ------------------------ | ---------------- | ----------------- |\n| prefixCls    | 组件 class 前缀          | string           | Yep-auto-complete |\n| style        | 组件样式                 | object           | {}                |\n| className    | 组件额外样式             | string           | -                 |\n| source       | 待自动完成的数据源       | array.isRequired | -                 |\n| value        | 输入框的值               | string           | -                 |\n| defaultValue | 输入框的默认值           | string           | -                 |\n| onChange     | 输入框内容变化的回调函数 | func             | -                 |\n| size         | 输入框的大小             | string           | -                 |\n| disabled     | 输入框是否禁用           | bool             | -                 |\n| placeholder  | 输入框的 placeholder     | string           | -                 |\n| clear        | 输入框是否显示清空按钮   | bool             | -                 |\n"},"badge":{"name":"badge","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Badge, List} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <List>\n          <List.Item extra=\"extra content\" arrow=\"horizontal\">\n            <Badge dot>\n              <span style={{width: '26px', height: '26px', background: '#ddd', display: 'inline-block'}} />\n            </Badge>\n            <span style={{marginLeft: 12}}>Dot badge</span>\n          </List.Item>\n          <List.Item extra=\"extra content\" arrow=\"horizontal\">\n            <Badge text={8}>\n              <span style={{width: '26px', height: '26px', background: '#ddd', display: 'inline-block'}} />\n            </Badge>\n            <span style={{marginLeft: 12}}>Text badge</span>\n          </List.Item>\n          <List.Item\n            thumb=\"https://zos.alipayobjects.com/rmsportal/faMhXAxhCzLvveJ.png\"\n            extra={<Badge text={77} max={55} />}\n            arrow=\"horizontal\"\n          >\n            Content\n          </List.Item>\n\n          <List.Item extra=\"extra\" arrow=\"horizontal\">\n            <Badge text={0} style={{marginLeft: 12}}>\n              Text number 0\n            </Badge>\n            <Badge text={'new'} style={{marginLeft: 12}} />\n          </List.Item>\n\n          <List.Item>\n            Customize\n            <Badge text=\"券\" style={{marginLeft: 12, padding: '0 3px', backgroundColor: '#f19736', borderRadius: 2}} />\n            <Badge text=\"NEW\" style={{marginLeft: 12, padding: '0 3px', backgroundColor: '#21b68a', borderRadius: 2}} />\n            <Badge\n              text=\"自动缴费\"\n              style={{\n                marginLeft: 12,\n                padding: '0 3px',\n                backgroundColor: '#fff',\n                borderRadius: 2,\n                color: '#f19736',\n                border: '1px solid #f19736',\n              }}\n            />\n          </List.Item>\n          <List.Item>\n            Customize\n            <Badge\n              text=\"凭处方退诊费\"\n              style={{\n                marginLeft: 5,\n                padding: '0 2px',\n                backgroundColor: '#fff',\n                borderRadius: 2,\n                fontSize: 11,\n                color: '#ED2945',\n                border: '1px solid #ED2945',\n              }}\n            />\n          </List.Item>\n        </List>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"徽标数","__content":"\n\n图标右上角的红点、数字或者文字。用于告知用户，该区域的状态变化或者待处理任务的数量。\n\n<DEMO>\n\n| 参数      | 说明                                                                             | 类型    | 默认值    |\n| --------- | -------------------------------------------------------------------------------- | ------- | --------- |\n| prefixCls | 组件 class 前缀                                                                  | string  | Yep-badge |\n| style     | 组件样式                                                                         | object  | {}        |\n| className | 组件额外样式                                                                     | string  | -         |\n| text      | 展示的数字或文案，当为数字时候，大于 overflowCount 时显示为 ${max}+，为 0 时隐藏 | string  | number    | - |\n| dot       | 不展示数字，只有一个小红点                                                       | boolean | -         |\n| max       | 展示封顶的数字值                                                                 | number  | 99        |\n| children  | 包裹的元素                                                                       | node    | -         |\n"},"button":{"name":"button","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport {Button, WhiteSpace} from '@jdcfe/yep-react';\nconst {ButtonGroup} = Button;\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Button\n          onClick={() => {\n            alert('default');\n          }}\n        >\n          default\n        </Button>\n        <WhiteSpace />\n        <Button disabled>default disabled</Button>\n        <WhiteSpace />\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            alert('primary');\n          }}\n        >\n          primary\n        </Button>\n        <WhiteSpace />\n        <Button type=\"primary\" disabled>\n          primary disabled\n        </Button>\n        <WhiteSpace />\n        <Button\n          type=\"ghost\"\n          onClick={() => {\n            alert('ghost');\n          }}\n        >\n          ghost\n        </Button>\n        <WhiteSpace />\n        <Button\n          type=\"ghost\"\n          disabled\n          onClick={() => {\n            alert('ghost');\n          }}\n        >\n          ghost disabled\n        </Button>\n        <WhiteSpace />\n        <Button size=\"md\" style={{backgroundColor: '#2A83E1', color: '#FFFFFF', border: 0}}>\n          下一步，上传医师认证相关证书\n        </Button>\n        <WhiteSpace />\n        <Button icon=\"lego_jiazai\">Loading Button</Button>\n        <WhiteSpace />\n        <Button icon=\"delete\">with icon</Button>\n        <WhiteSpace />\n        <Button size=\"sm\" inline type=\"primary\">\n          small\n        </Button>\n        <Button size=\"sm\" inline>\n          small\n        </Button>\n        <WhiteSpace />\n        <Button inline>inline</Button>\n        <WhiteSpace />\n        <Button submit>Submit</Button>\n        <WhiteSpace />\n        <ButtonGroup>\n          <Button>确定</Button>\n          <Button type=\"primary\">取消</Button>\n        </ButtonGroup>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"按钮","__content":"\n\n<DEMO>\n\n| 参数      | 说明                                              | 类型    | 默认值  |\n| --------- | ------------------------------------------------- | ------- | ------- |\n| prefixCls | 组件 class 前缀                                   | string  | Yep-btn |\n| style     | 组件样式                                          | object  | {}      |\n| className | 额外要添加的 CSS 类名                             | string  | -       |\n| disabled  | 是否不可用                                        | boolean | false   |\n| type      | 按钮类型,可选值为 primary/ghost 或者不设          | string  | -       |\n| size      | 按钮大小,可选值为 sm、md                          | string  | -       |\n| onClick   | 点击按钮的点击回调函数                            | func    | -       |\n| inline    | 是否设置为行内按钮                                | boolean | false   |\n| icon      | 可以是 Icon 组件里内置的某个 icon 的 type 值      | string  | -       |\n| submit    | 是否是提交按钮，如果是，则是 button type=‘submit’ | boolean | false   |\n"},"carousel":{"name":"carousel","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Carousel} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Carousel className=\"bbb\" isInfinite={true}>\n          <div>\n            <img src=\"//m.360buyimg.com/babel/jfs/t23224/35/1026004507/74414/35929bac/5b4d885bN0cdaa9f4.jpg\" />\n          </div>\n          <div>\n            <img src=\"//m.360buyimg.com/babel/jfs/t21421/47/1859879985/100913/f5dd5cb5/5b3caa69N4c7a4999.jpg\" />\n          </div>\n          <div>\n            <img src=\"//img1.360buyimg.com/pop/jfs/t23260/244/889866376/102314/ef6393ec/5b46cb8cN1804aa98.jpg\" />\n          </div>\n        </Carousel>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"轮播图","__content":"\n\n<DEMO>\n\n| 参数            | 说明                                        | 类型          | 默认值       |\n| --------------- | ------------------------------------------- | ------------- | ------------ |\n| prefixCls       | 组件 class 前缀                             | string        | Yep-carousel |\n| autoPlay        | 不设定不会自动轮播，如需轮播传时间，单位 ms | bool / number | false        |\n| className       | 额外要添加的 CSS 类名                       | string        | -            |\n| initPage        | 默认第一帧                                  | number        | 0            |\n| onTransitionEnd | 每次轮播完一帧的钩子函数                    | function      | null         |\n| isBounces       | 是否允许回弹效果                            | bool          | true         |\n| isInfinite      | 是否无限滚动                                | bool          | false        |\n"},"checkbox":{"name":"checkbox","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport {Checkbox} from '@jdcfe/yep-react';\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Checkbox>鱼</Checkbox>\n        <Checkbox disabled>肉</Checkbox>\n        <Checkbox defaultChecked>小龙虾</Checkbox>\n        <Checkbox checked>哈啤酒</Checkbox>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"复选框","__content":"\n\n<DEMO>\n\n| 参数           | 说明                      | 类型    | 默认值       |\n| -------------- | ------------------------- | ------- | ------------ |\n| prefixCls      | 组件 class 前缀           | string  | Yep-checkbox |\n| style          | 组件样式                  | object  | {}           |\n| className      | 额外要添加的 CSS 类名     | string  | -            |\n| name           | 表单中 input 的 name      | string  | -            |\n| defaultChecked | 是否默认选中              | boolean | -            |\n| checked        | 是否选中                  | boolean | -            |\n| disabled       | 是否不可用                | boolean | -            |\n| onChange       | change 事件触发的回调函数 | func    | -            |\n"},"count-down":{"name":"count-down","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {CountDown, WhiteSpace} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <p>使用dayStr, hourStr, minuteStr, secondStr字段</p>\n        <CountDown leftTime={100000}>\n          {({dayStr, hourStr, minuteStr, secondStr}) => (\n            <p>\n              倒计时剩余： {dayStr}天{hourStr}小时{minuteStr}分钟{secondStr}秒\n            </p>\n          )}\n        </CountDown>\n        <WhiteSpace />\n        <p>使用dayNum, hourNum, minuteNum, secondNum字段</p>\n        <CountDown leftTime={100000}>\n          {({dayNum, hourNum, minuteNum, secondNum}) => (\n            <p>\n              倒计时剩余： {dayNum}天{hourNum}小时{minuteNum}分钟{secondNum}秒\n            </p>\n          )}\n        </CountDown>\n        <WhiteSpace />\n        <p>自定义结束文案</p>\n        <CountDown leftTime={5} overText=\"哈哈哈，终于结束啦\">\n          {({dayNum, hourNum, minuteNum, secondNum}) => (\n            <span>\n              倒计时剩余： {dayNum}天{hourNum}小时{minuteNum}分钟{secondNum}秒\n            </span>\n          )}\n        </CountDown>\n        <WhiteSpace />\n        <p>倒计时结束回调</p>\n        <CountDown leftTime={15} onEnd={() => console.log('onEnd回调执行啦')}>\n          {({dayNum, hourNum, minuteNum, secondNum}) => (\n            <span>\n              倒计时剩余： {dayNum}天{hourNum}小时{minuteNum}分钟{secondNum}秒\n            </span>\n          )}\n        </CountDown>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"倒计时","__content":"\n\n倒计时组件\n\n<DEMO>\n\n| 参数      | 说明                         | 类型     | 默认值         |\n| --------- | ---------------------------- | -------- | -------------- |\n| prefixCls | 组件 class 前缀              | string   | Yep-count-down |\n| style     | 组件样式                     | object   | {}             |\n| className | 组件额外样式                 | string   | -              |\n| leftTime  | 需要倒计时的时间，单位为秒。 | number   | -              |\n| onEnd     | 倒计时结束时的回调函数       | function | () => null     |\n| overText  | 倒计时结束后展示文案。       | string   | 活动结束       |\n\n## children props\n\n| 参数      | 说明                               | 类型   | 默认值 |\n| --------- | ---------------------------------- | ------ | ------ |\n| dayStr    | 剩余天数                           | string | -      |\n| hourStr   | 剩余小时数(不足 10,自动在左侧加 0) | string | -      |\n| minuteStr | 剩余分钟数(不足 10,自动在左侧加 0) | string | -      |\n| secondStr | 剩余秒数(不足 10,自动在左侧加 0)   | string | -      |\n| dayNum    | 剩余天数                           | number | 0      |\n| hourNum   | 剩余小时数                         | number | 0      |\n| minuteNum | 剩余分钟数                         | number | 0      |\n| secondNum | 剩余秒数。                         | number | 0      |\n\ns\n"},"date-picker":{"name":"date-picker","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {DatePicker, Button} from '@jdcfe/yep-react';\nconst nowTimeStamp = Date.now();\nconst now = new Date(nowTimeStamp);\nconst utcNow = new Date(now.getTime() + now.getTimezoneOffset() * 60000);\nclass Demo extends React.PureComponent {\n  state = {\n    show: false,\n    date: now,\n    time: now,\n    utcDate: utcNow,\n    dpValue: null,\n    customChildValue: null,\n    visible: false,\n  };\n  render() {\n    return (\n      <div>\n        <Button onClick={() => this.setState({show: true})}>选择时间</Button>\n        <DatePicker\n          mode={'date'}\n          show={this.state.show}\n          value={this.state.date}\n          onOk={value => {\n            this.setState({show: false});\n            console.log(value);\n          }}\n          onCancel={() => this.setState({show: false})}\n          onChange={date => this.setState({date})}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"日期选择","__content":"\n\n用于选择日期或者时间。\n\n<DEMO>\n\n| 参数          | 说明                                                                               | 类型                               | 默认值      |\n| ------------- | ---------------------------------------------------------------------------------- | ---------------------------------- | ----------- |\n| mode          | 日期选择的类型, 可以是日期`date`,时间`time`,日期+时间`datetime`,年`year`,月`month` | String                             | `date`      |\n| value         | 当前选中时间                                                                       | Date                               | 无          |\n| minDate       | 最小可选日期                                                                       | Date                               | 2000-1-1    |\n| maxDate       | 最大可选日期                                                                       | Date                               | 2030-1-1    |\n| use12Hours    | 12 小时制                                                                          | Boolean                            | false       |\n| minuteStep    | 分钟数递增步长设置                                                                 | Number                             | 1           |\n| disabled      | 是否不可用                                                                         | Boolean                            | false       |\n| onChange      | 时间发生变化的回调函数                                                             | (date: Object): void               | -           |\n| onValueChange | 每列 picker 改变时的回调                                                           | (vals: any, index: number) => void | -           |\n| title         | 弹框的标题                                                                         | string/React.ReactElement          | 无          |\n| prefixCls     | class 前缀                                                                         | string                             | `am-picker` |\n| className     | 样式类名                                                                           | string                             | -           |\n| onOk          | 点击选中时执行的回调                                                               | (val): void                        | 无          |\n| onCancel      | 点击取消时执行的回调                                                               | (): void                           | 无          |\n\n注意：日期字符串在不同浏览器有不同的实现，例如 `new Date('2017-1-1')` 在 Safari 上是 Invalid Date，而在 Chrome 上是能正常解析的。\n"},"dialog":{"name":"dialog","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Dialog, Button, WhiteSpace} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    show: false,\n  };\n\n  render() {\n    const obj = {\n      className: '123',\n    };\n    return (\n      <div title=\"Dialog\">\n        <Button\n          onClick={() => {\n            this.setState({show: true});\n          }}\n        >\n          Dialog\n        </Button>\n        <WhiteSpace />\n        <Button\n          onClick={() => {\n            Dialog.confirm('标题', '确定要删除吗?');\n          }}\n        >\n          Confirm\n        </Button>\n        <WhiteSpace />\n        <Button\n          onClick={() => {\n            Dialog.confirm('标题', '确定要删除吗?', [{text: '好的', onClick: () => console.log('好的')}], obj);\n          }}\n        >\n          Alert\n        </Button>\n\n        <Dialog\n          show={this.state.show}\n          maskCloseable\n          onClose={() => {\n            this.setState({show: false});\n          }}\n        >\n          <div className={`Yep-dialog-alert-content`}>123</div>\n        </Dialog>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"对话框","__content":"\n\n<DEMO>\n\nDialog\n\n| 参数             | 说明                  | 类型    | 默认值     |\n| ---------------- | --------------------- | ------- | ---------- |\n| prefixCls        | 组件 class 前缀       | string  | Yep-dialog |\n| style            | 组件样式              | object  | {}         |\n| bodyStyle        | body 组件样式         | object  | {}         |\n| className        | 额外要添加的 CSS 类名 | string  | -          |\n| title            | 对话框的标题          | string  | -          |\n| footer           | 底部组件              | node    | -          |\n| onClose          | 关闭事件的回调函数    | func    | -          |\n| show             | 是否显示              | boolean | false      |\n| maskCloseable    | 点击蒙层是否关闭      | boolean | false      |\n| dialogTransition | 对话框动画            | string  | zoom       |\n| maskTransition   | 蒙层对话              | string  | fade       |\n\nDialog.confirm\n\n| 参数      | 说明                               | 类型         | 默认值                                                                                                                               |\n| --------- | ---------------------------------- | ------------ | ------------------------------------------------------------------------------------------------------------------------------------ |\n| title     | 标题                               | string       | -                                                                                                                                    |\n| message   | 提示信息                           | string、node | -                                                                                                                                    |\n| actions   | 按钮组，{text, onClick,className}, | Array        | `[{text: 'Cancel', onClick: () => console.log('cancel'),className:''},{text: 'Ok', onClick: () => console.log('ok'),className:''},]` |\n| restProps | dialog 组件其他参数组成的对象      | object       | {}                                                                                                                                   |\n\n`Dialog.confirm(title, message, actions?, restProps?).close()` 可以在外部关闭 confirm\n\nrestProps:\n\n| 参数             | 说明                  | 类型    | 默认值 |\n| ---------------- | --------------------- | ------- | ------ |\n| style            | 组件样式              | object  | {}     |\n| bodyStyle        | body 组件样式         | object  | {}     |\n| className        | 额外要添加的 CSS 类名 | string  | -      |\n| maskCloseable    | 点击蒙层是否关闭      | boolean | false  |\n| dialogTransition | 对话框动画            | string  | zoom   |\n| maskTransition   | 蒙层对话              | string  | fade   |\n"},"dropdown":{"name":"dropdown","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Dropdown} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Dropdown overlay={'这是一个下拉框'}>\n          <button>Open</button>\n        </Dropdown>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"下拉框","__content":"\n\n下拉框\n\n<DEMO>\n\n| 参数      | 说明                            | 类型                                                | 默认值       | 必填  |\n| --------- | ------------------------------- | --------------------------------------------------- | ------------ | ----- |\n| prefixCls | 组件 class 前缀                 | string                                              | Yep-dropdown | false |\n| style     | 组件样式                        | object                                              | {}           | false |\n| className | 组件额外样式                    | string                                              | -            | false |\n| overlay   | 弹出层内容                      | node                                                | -            | true  |\n| direction | 出现方向                        | oneOf(['up', 'down', 'left', 'right'])              | `down`       | false |\n| align     | 对齐方式                        | oneOf(['top', 'right', 'bottom', 'left', 'middle']) | `left`       | false |\n| open      | 下拉展开状态                    | bool                                                | -            | false |\n| onToggle  | 切换 open 状态后的回调          | func                                                | -            | false |\n| aligned   | 触发组件 与 弹出层内容 宽度相同 | bool                                                | `false`      | false |\n"},"gesture":{"name":"gesture","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Gesture, WhiteSpace} from '@jdcfe/yep-react';\nconst style = `\n  .outter {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin: 20px auto;\n    width: 80%;\n    height: 40px;\n    border-width: 1px;\n    border-color: red;\n    border-style: solid;\n    overflow: hidden;\n  }\n  .inner {\n    width: 80%;\n    height: 80%;\n    background-color: black;\n  }\n  .swiper-container{\n    margin: 20px 0;\n  }\n  .swiper{\n    display: flex;\n    align-items: center;\n    text-align: center;\n    background-color: #CCC;\n    width: 100%;\n    height: 100%;\n  }\n`;\n\nclass Demo extends React.Component {\n  root;\n  rootNode;\n  _scale;\n  _rotation;\n  _x;\n  _y;\n\n  constructor(props) {\n    super(props);\n  }\n  log = (type, keys) => (...args) => {\n    window.requestAnimationFrame(() => {\n      this.doLog(type, keys, ...args);\n      this.doTransform(type, ...args);\n    });\n  };\n  doLog = (type, keys, ...args) => {\n    const extInfo = keys ? keys.map(key => `${key} = ${args[0][key]}`).join(', ') : '';\n    const logEl = this.refs.log;\n    logEl.innerHTML += `<p>${type} ${extInfo}</p>`;\n    logEl.scrollTop = logEl.scrollHeight;\n  };\n  doTransform = (type, ...args) => {\n    if (type === 'onPinch') {\n      const {scale} = args[0];\n      this._scale = scale;\n    }\n    if (type === 'onRotate') {\n      const {rotation} = args[0];\n      this._rotation = rotation;\n    }\n    if (type === 'onPan') {\n      const {x, y} = args[0].moveStatus;\n      this._x = x;\n      this._y = y;\n    }\n    if (type === 'onPanEnd' || type === 'onPanCancel') {\n      const {x, y} = args[0].moveStatus;\n      this._x = 0;\n      this._y = 0;\n    }\n    let transform = [];\n    this._scale && transform.push(`scale(${this._scale})`);\n    this._rotation && transform.push(`rotate(${this._rotation}deg)`);\n    typeof this._x === 'number' && transform.push(`translateX(${this._x}px)`);\n    typeof this._y === 'number' && transform.push(`translateY(${this._y}px)`);\n    transform = transform.join(' ');\n    this.rootNode = ReactDOM.findDOMNode(this.root);\n    this.rootNode.style.transform = transform;\n  };\n  moveSwiper(e) {\n    const {srcEvent, moveStatus} = e;\n    const {x, y} = e.moveStatus;\n\n    this.swiperNode = ReactDOM.findDOMNode(this.refSwiper);\n    this.swiperNode.style.transform = [`translateX(${x}px)`];\n\n    // preventDefault, avoid trigger scroll event when touch moving.\n    srcEvent.preventDefault();\n  }\n\n  resetSwiper() {\n    this.swiperNode = ReactDOM.findDOMNode(this.refSwiper);\n    this.swiperNode.style.transform = [`translateX(0px)`];\n  }\n\n  render() {\n    return (\n      <div>\n        <style dangerouslySetInnerHTML={{__html: style}} />\n        <div ref=\"log\" style={{height: 100, overflow: 'auto', margin: 10}} />\n        <div className=\"outter\">\n          <Gesture\n            direction=\"all\"\n            enablePinch\n            enableRotate\n            onTap={this.log('onTap')}\n            onPress={this.log('onPress')}\n            onPressUp={this.log('onPressUp')}\n            onSwipe={this.log('onSwipe', ['direction'])}\n            onSwipeLeft={this.log('onSwipeLeft', ['direction'])}\n            onSwipeRight={this.log('onSwipeRight', ['direction'])}\n            onSwipeUp={this.log('onSwipeUp', ['direction'])}\n            onSwipeDown={this.log('onSwipeDown', ['direction'])}\n            onPinch={this.log('onPinch', ['scale'])}\n            onPinchStart={this.log('onPinchStart', ['scale'])}\n            onPinchMove={this.log('onPinchMove', ['scale'])}\n            onPinchEnd={this.log('onPinchEnd', ['scale'])}\n            onPinchCancel={this.log('onPinchCancel', ['scale'])}\n            onPinchIn={this.log('onPinchIn', ['scale'])}\n            onPinchOut={this.log('onPinchOut', ['scale'])}\n            onRotate={this.log('onRotate', ['rotation'])}\n            onRotateStart={this.log('onRotateStart', ['rotation'])}\n            onRotateMove={this.log('onRotateMove', ['rotation'])}\n            onRotateEnd={this.log('onRotateEnd', ['rotation'])}\n            onRotateCancel={this.log('onRotateCancel', ['rotation'])}\n            onPan={this.log('onPan', ['moveStatus', 'direction'])}\n            onPanStart={this.log('onPanStart', ['moveStatus', 'direction'])}\n            onPanMove={this.log('onPanMove', ['moveStatus', 'direction'])}\n            onPanEnd={this.log('onPanEnd', ['moveStatus', 'direction'])}\n            onPanCancel={this.log('onPanCancel', ['moveStatus', 'direction'])}\n            onPanLeft={this.log('onPanLeft', ['moveStatus', 'direction'])}\n            onPanRight={this.log('onPanRight', ['moveStatus', 'direction'])}\n            onPanUp={this.log('onPanUp', ['moveStatus', 'direction'])}\n            onPanDown={this.log('onPanDown', ['moveStatus', 'direction'])}\n          >\n            <div\n              className=\"inner\"\n              ref={el => {\n                this.root = el;\n              }}\n            />\n          </Gesture>\n        </div>\n        <div className=\"swiper-container\">\n          <Gesture\n            direction=\"horizontal\"\n            onPanMove={(e, args) => {\n              this.moveSwiper(e, args);\n            }}\n            onPanEnd={() => {\n              this.resetSwiper();\n            }}\n            onTouchMove={e => {\n              console.log('still run touch move');\n            }}\n          >\n            <div style={{height: 200, backgroundColor: 'red'}}>\n              <div\n                className=\"swiper\"\n                ref={e => {\n                  this.refSwiper = e;\n                }}\n              >\n                This is simple swiper demo. Only allow horizontal direction and height=200px to test scroll event.\n              </div>\n            </div>\n          </Gesture>\n        </div>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"手势操作","__content":"\n\n为移动端 React 组件提供手势操作，inspired by hammer and\n\n<DEMO>\n\n## Properties\n\n### common props\n\n| 参数      | 类型   | 默认值 | 说明                                                        |\n| --------- | ------ | ------ | ----------------------------------------------------------- |\n| direction | string | `all`  | 控制允许的手势方向，可选`['all', 'vertical', 'horizontal']` |\n\n### Swipe\n\n| 参数         | 类型     | 默认值 | 说明                                                                     |\n| ------------ | -------- | ------ | ------------------------------------------------------------------------ |\n| onSwipe      | function | -      | swipe callback, will triggered at the same time of all of below callback |\n| onSwipeLeft  | function | -      | swipe left callback                                                      |\n| onSwipeRight | function | -      | swipe right callback                                                     |\n| onSwipeUp    | function | -      | swipe Up callback                                                        |\n| onSwipeDown  | function | -      | swipe down callback                                                      |\n\n### Pan\n\n| 参数        | 类型     | 默认值 | 说明                                                                   |\n| ----------- | -------- | ------ | ---------------------------------------------------------------------- |\n| onPan       | function |        | pan callback, will triggered at the same time of all of below callback |\n| onPanStart  | function |        | drag start callback                                                    |\n| onPanMove   | function |        | drag move callback                                                     |\n| onPanEnd    | function |        | drag end callback                                                      |\n| onPanCancel | function |        | drag cancel callback                                                   |\n| onPanLeft   | function |        | pan left callback                                                      |\n| onPanRight  | function |        | pan right callback                                                     |\n| onPanUp     | function |        | pan up callback                                                        |\n| onPanDown   | function |        | pan down callback                                                      |\n\n### Pinch\n\npinch gesture is not enabled by default, you must set props.enablePinch = true at first;\n| 参数 | 类型 | 默认值 | 说明 |\n| ----------- | -------- | ------ | ---------------------------------------------------------------------- |\n| onPinch | function | | pinch callback, will triggered at the same time of all of below callback |\n| onPinchStart | function | | pinch start callback |\n| onPinchMove | function | | pinch move callback |\n| onPinchEnd | function | | pinch end callback |\n| onPinCancel | function | | pinch cancel callback |\n| onPinchIn | function | |pinch in callback |\n| onPinchOut | function | | pinch out callback |\n\n### Rotate\n\nRotate gesture is not enabled by default, you must set props.enableRotate = true at first;\n\n| 参数           | 类型     | 默认值               | 说明                                                                      |\n| -------------- | -------- | -------------------- | ------------------------------------------------------------------------- |\n| onRotate       | function |                      | rotate callback, will triggered at the same time of all of below callback |\n| onRotateStart  | function |                      | rotate start callback                                                     |\n| onRotateMove   | function | rotate move callback |\n| onRotateEnd    | function |                      | rotate end callback                                                       |\n| onRotateCancel | function |                      | rotate cancel callback                                                    |\n\n### gesture\n\n```javascript\n// http://hammerjs.github.io/api/#event-object\nexport type GestureStatus = {\n/_ start status snapshot _/\nstartTime: number,\nstartTouches: Finger[],\n\nstartMutliFingerStatus: MultiFingerStatus[],\n\n/_ now status snapshot _/\ntime: number,\ntouches: Finger[],\npreTouches: Finger[],\n\nmutliFingerStatus: MultiFingerStatus[],\n\n/_ delta status from touchstart to now, just for singe finger _/\nmoveStatus: SingeFingerMoveStatus,\n\n/_ whether is a long tap _/\npress: boolean,\n\n/_ whether is a pan _/\npan: boolean,\n/_ whether is an available pan _/\navailablePan: boolean,\n\n/_ whether is a swipe _/\nswipe: boolean,\ndirection: number,\n\n/_ whether is in pinch process _/\npinch: boolean,\nscale: number,\n\n/_ whether is in rotate process _/\nrotate: boolean,\nrotation: number, // Rotation (in deg) that has been done when multi-touch. 0 on a single touch.\n/_ event, such as TouchEvent, MouseEvent, PointerEvent _/\nsrcEvent: any,\n};\n```\n"},"icon":{"name":"icon","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Icon} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    const icons = [\n      'add-circle',\n      'add-user',\n      'add-users',\n      'add',\n      'arrow-back',\n      'arrow-down-hollow',\n      'arrow-down',\n      'arrow-right',\n      'arrow-up',\n      'baitiao',\n      'box',\n      'calculator',\n      'calendar',\n      'calendar2',\n      'cancel',\n      'category',\n      'charge',\n      'close',\n      'confirm',\n      'credit-card',\n      'data',\n      'delete-with-bg',\n      'delete',\n      'doctorial-hat',\n      'dongdong',\n      'email',\n      'flag',\n      'follow',\n      'heart',\n      'help',\n      'invoice',\n      'invoice2',\n      'item-checked',\n      'jimi',\n      'lego_bianji',\n      'lego_biaoge',\n      'lego_cuowu1',\n      'lego_cuowu2',\n      'lego_dingwei',\n      'lego_duihao2',\n      'lego_duohao1',\n      'lego_fangda',\n      'lego_fasong',\n      'lego_fenxiang',\n      'lego_gengduo',\n      'lego_gouwuche',\n      'lego_guanbi',\n      'lego_guanzhu',\n      'lego_jianshao',\n      'lego_jiazai',\n      'lego_jingxuan',\n      'lego_liuyan',\n      'lego_mima',\n      'lego_mokuai',\n      'lego_quanping',\n      'lego_remai',\n      'lego_rili',\n      'lego_saoma',\n      'lego_shanchu',\n      'lego_shangchuan',\n      'lego_shangjiantou',\n      'lego_shezhi',\n      'lego_shijian',\n      'lego_shoucang',\n      'lego_shouqi',\n      'lego_shuaxin',\n      'lego_sousuo',\n      'lego_suoxiao',\n      'lego_tianjia',\n      'lego_tupiao',\n      'lego_wenjian',\n      'lego_xiajiantou',\n      'lego_xiangshang',\n      'lego_xiangxia',\n      'lego_xiangyou',\n      'lego_xiangzuo',\n      'lego_xianshi',\n      'lego_xiazai',\n      'lego_xinpinshangshi',\n      'lego_yichang',\n      'lego_yonghu',\n      'lego_youjian',\n      'lego_youjianotu',\n      'lego_youshuangjiantou',\n      'lego_zanting',\n      'lego_zengjia',\n      'lego_zhekou',\n      'lego_zhidi',\n      'lego_zhiding',\n      'lego_zuojiantou',\n      'lego_zuoshuangjiantou',\n      'list-download',\n      'list',\n      'location',\n      'medal',\n      'modify',\n      'notice',\n      'oder',\n      'order',\n      'org',\n      'people',\n      'plus',\n      'score',\n      'shop-baocun',\n      'shop-beifen',\n      'shop-bianji',\n      'shop-chenggongyuandi',\n      'shop-chiping',\n      'shop-cuxiao',\n      'shop-dakaixinshuangkou',\n      'shop-danpin',\n      'shop-daochu',\n      'shop-goumai',\n      'shop-guanbi',\n      'shop-guanbitongzhi',\n      'shop-jiangxu',\n      'shop-jinzhi',\n      'shop-jinzhiyuandi',\n      'shop-kongzhuangtai',\n      'shop-paixu',\n      'shop-pinpai',\n      'shop-qingcang',\n      'shop-qingchu',\n      'shop-resouci',\n      'shop-rili',\n      'shop-shaixuan',\n      'shop-shanchu',\n      'shop-shangpin',\n      'shop-shangsheng',\n      'shop-shengxu',\n      'shop-shezhi',\n      'shop-shibai',\n      'shop-shibaiyuandi',\n      'shop-shuoming',\n      'shop-sousuo',\n      'shop-tishi',\n      'shop-tishiyuandi',\n      'shop-tuichu',\n      'shop-tuijian',\n      'shop-xiajiang',\n      'shop-xiazai',\n      'shop-yidong',\n      'shop-yishaixuan',\n      'shop-yiwen',\n      'shop-yiwenyuandi',\n      'shop-yonghu',\n      'shop-youjian',\n      'shop-youjianquxiao',\n      'shop-yunying',\n      'shop-zhengque',\n      'shop-zhuyi',\n      'shop-zhuyiyuandi',\n      'shop-zidingyi',\n      'shop-zidingyiqu',\n      'signOut',\n      'tip',\n      'training',\n      'trophy',\n      'view',\n      'wallet',\n      'zoom',\n    ];\n\n    return <div>{icons.map(icon => <Icon type={icon} key={icon} />)}</div>;\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"图标","__content":"\n\n<DEMO>\n\n| 参数      | 说明           | 类型                               | 默认值   |\n| --------- | -------------- | ---------------------------------- | -------- |\n| className | 组件额外 class | string                             | Yep_icon |\n| type      | icon 名称      | string                             | -        |\n| size      | 图标大小       | string('xxs'/'xs'/'sm'/'md'/'lg' ) | md       |\n| color     | 图标颜色       | string                             | -        |\n"},"indexed-list":{"name":"indexed-list","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {IndexedList, List, Sticky} from '@jdcfe/yep-react';\nconst {Item} = List;\nconst {StickyContainer} = Sticky;\n\nclass Header extends React.Component {\n  static defaultProps = {\n    className: '',\n  };\n\n  componentWillReceiveProps(nextProps) {\n    if (nextProps.isSticky !== this.props.isSticky && nextProps.isSticky) {\n      console.log(nextProps.isSticky);\n      this.props.change(nextProps.sectionData);\n    }\n  }\n  render() {\n    const {style, sectionData} = this.props;\n    return (\n      <div\n        className=\"sticky\"\n        style={{\n          ...style,\n          zIndex: 3,\n          backgroundColor: sectionData.charCodeAt(0) % 2 ? '#5890ff' : '#F8591A',\n          color: 'white',\n        }}\n      >\n        {sectionData}\n      </div>\n    );\n  }\n}\n\nclass Demo extends React.PureComponent {\n  state = {\n    key: '',\n  };\n\n  change = key => {\n    this.setState({key});\n  };\n\n  render() {\n    const data = {\n      A: [\n        {label: '安徽省', id: '34'},\n        {label: '安徽省0', id: '340'},\n        {label: '安徽省1', id: '341'},\n        {label: '安徽省2', id: '342'},\n        {label: '安徽省3', id: '343'},\n      ],\n      B: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      C: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      D: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      E: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      F: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      G: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      H: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      I: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n      J: [\n        {label: '北京市', id: '01'},\n        {label: '北京市0', id: '340'},\n        {label: '北京市1', id: '341'},\n        {label: '北京市2', id: '342'},\n        {label: '北京市3', id: '343'},\n      ],\n    };\n    return (\n      <div>\n        <IndexedList\n          showIndicator\n          indexedBarStyle={{top: 170}}\n          data={data}\n          activeBar={this.state.key}\n          renderRow={rowData => <Item>{rowData.label}</Item>}\n          enableQuickIndexedBarTop={false}\n          onQuickSearch={this.change}\n          keyLabel=\"id\"\n          renderSectionWrapper={sectionId => (\n            <StickyContainer key={`s_${sectionId}_c`} className=\"sticky-container\" style={{zIndex: 4}} />\n          )}\n          renderSectionHeader={sectionData => (\n            <Sticky>\n              {({style, isSticky}) => (\n                <Header change={this.change} sectionData={sectionData} style={style} isSticky={isSticky} />\n              )}\n            </Sticky>\n          )}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"索引列表","__content":"\n\n此组件常用于 “通讯录”/“城市列表” 等场景中，支持索引导航功能。\n\n<DEMO>\n\n| 参数                     | 说明                                                                                             | 类型                               | 默认值                                    |\n| ------------------------ | ------------------------------------------------------------------------------------------------ | ---------------------------------- | ----------------------------------------- |\n| prefixCls                | 组件 class 前缀                                                                                  | string                             | Yep-indexed-list                          |\n| style                    | 组件样式                                                                                         | object                             | {}                                        |\n| className                | 组件额外样式                                                                                     | string                             | -                                         |\n| showIndicator            | 是否显示指示器                                                                                   | boolean                            | false                                     |\n| indicatorStyle           | 指示器样式                                                                                       | object                             | {}                                        |\n| data                     | 数据                                                                                             | object                             | `required`                                |\n| renderSectionHeader      | 如果提供了此函数，会为每个小节(section)渲染一个标题                                              | func                               | (sectionData, sectionID) => renderable    |\n| renderRow                | 从数据源中接受一条数据，以及它和它所在 section 的 ID。返回一个可渲染的组件来为这行数据进行渲染。 | func                               | (rowData, sectionID, rowID) => renderable |\n| renderSectionWrapper     | 渲染自定义的区块包裹组件                                                                         | func                               | (sectionID) => renderable                 |\n| renderSectionBodyWrapper | 渲染自定义的区块 body 包裹组件                                                                   | object                             | (sectionID) => renderable                 |\n| quickIndexedBarTop       | 快捷导航栏最顶部按钮、常用于回到顶部                                                             | object{value:string, label:string} | { value: '#', label: '#' }                |\n| indexedBarStyle          | 快捷导航栏 的 style                                                                              | object                             | { }                                       |\n| useBodyScroll            | 使用 html 的 body 作为滚动容器                                                                   | boolean                            | true                                      |\n| onQuickSearch            | 快捷导航切换时调用                                                                               | func                               | (sectionID: any, topId?:any) => void      |\n| renderHeader             | 自定义页头                                                                                       | func                               | () => renderable                          |\n| renderFooter             | 自定义页脚                                                                                       | func                               | () => renderable                          |\n| renderBodyComponent      | 自定义 body 的包裹组件                                                                           | func                               | () => renderable                          |\n| enableQuickIndexedBarTop | 是否显示默认快速回到顶部的 key                                                                   | bool                               | true                                      |\n| activeBar                | 外部传进来的选中索引的值                                                                         | string                             | -                                         |\n| keyLabel                 | 作为唯一的 key                                                                                   | string                             | `value`                                   |\n\n## Attributes\n\n```\ndata: {\n      A: [\n        {label: '安徽省', value: '34'},\n        {label: '安徽省0', value: '340'},\n        {label: '安徽省1', value: '341'},\n        {label: '安徽省2', value: '342'},\n        {label: '安徽省3', value: '343'},\n      ],\n      B: [\n        {label: '北京市', value: '01'},\n        {label: '北京市0', value: '010'},\n        {label: '北京市1', value: '011'},\n        {label: '北京市2', value: '012'},\n        {label: '北京市3', value: '013'},\n      ],\n      C: [\n        {label: '重庆市', value: '02'},\n        {label: '重庆市0', value: '020'},\n        {label: '重庆市1', value: '021'},\n        {label: '重庆市2', value: '022'},\n        {label: '重庆市3', value: '023'},\n      ],\n    }\n```\n"},"infinite-loader":{"name":"infinite-loader","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {InfiniteLoader} from '@jdcfe/yep-react';\nconst style = {\n  height: 50,\n  border: '1px solid green',\n  margin: 6,\n  padding: 8,\n};\nclass Demo extends React.PureComponent {\n  state = {\n    items: Array.from({length: 20}),\n  };\n\n  fetchMoreData = () => {\n    // a fake async api call like which sends\n    // 20 more records in 1.5 secs\n    setTimeout(() => {\n      this.setState({\n        items: this.state.items.concat(Array.from({length: 20})),\n      });\n    }, 1500);\n  };\n\n  render() {\n    return (\n      <div>\n        <InfiniteLoader\n          dataLength={this.state.items.length}\n          loadMore={this.fetchMoreData}\n          hasMore={true}\n          loader={<h4>Loading...</h4>}\n        >\n          {this.state.items.map((i, index) => (\n            <div style={style} key={index}>\n              div - #{index}\n            </div>\n          ))}\n        </InfiniteLoader>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"下拉刷新","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {InfiniteLoader} from '@jdcfe/yep-react';\nconst style = {\n  height: 50,\n  border: '1px solid green',\n  margin: 6,\n  padding: 8,\n};\nclass Demo extends React.PureComponent {\n  state = {\n    items: Array.from({length: 20}),\n  };\n\n  fetchMoreData = () => {\n    // a fake async api call like which sends\n    // 20 more records in 1.5 secs\n    setTimeout(() => {\n      this.setState({\n        items: this.state.items.concat(Array.from({length: 20})),\n      });\n    }, 1500);\n  };\n\n  refresh = () => {\n    setTimeout(() => {\n      this.setState({\n        items: Array.from({length: 20}),\n      });\n    }, 1500);\n  };\n\n  render() {\n    return (\n      <div>\n        <InfiniteLoader\n          dataLength={this.state.items.length}\n          loadMore={this.fetchMoreData}\n          hasMore={true}\n          loader={<h4>Loading...</h4>}\n          pullDownToRefresh\n          refreshFunction={this.refresh}\n          pullDownToRefreshThreshold={300}\n        >\n          {this.state.items.map((i, index) => (\n            <div style={style} key={index}>\n              div - #{index}\n            </div>\n          ))}\n        </InfiniteLoader>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"滚动无限加载","__content":"\n\n滚动无限加载\n\n<DEMO>\n\n| 参数                       | 说明                                                            | 类型    | 默认值              | required | -------- |\n| -------------------------- | --------------------------------------------------------------- | ------- | ------------------- | -------- | -------- |\n| children                   | 列表                                                            | node    | -                   | true     |\n| loadMore                   | 请求更多数据调用的方法                                          | func    | -                   | true     |\n| hasMore                    | 是否还有更多数据                                                | boolean | true                | false    |\n| loader                     | 等待请求时显示在最底部的组件                                    | node    | -                   | false    |\n| scrollThreshold            | 列表滚动到多少时请求更多数据                                    | number  | 0.8                 | false    |\n| endMessage                 | 数据全部加载完成时显示在底部的组件                              | node    | -                   | false    |\n| style                      | 额外样式                                                        | object  | {}                  | false    |\n| className                  | 额外 class                                                      | string  | Yep-infinite-loader | false    |\n| height                     | 滚动加载列表的固定高度                                          | number  | -                   | false    |\n| scrollableTarget           | 滚动的父容器 id                                                 | string  | -                   | false    |\n| hasChildren                | 如果 children 不是数组，提供额外的 prop 来计算数据是 0 还是更多 | boolean | -                   | false    |\n| pullDownToRefresh          | 是否下拉刷新数据                                                | boolean | -                   | false    |\n| pullDownToRefreshContent   | 下拉刷新数据的提示文案组件                                      | node    | -                   | false    |\n| releaseToRefreshContent    | 松开刷新数据显示的文案组件                                      | node    | -                   | false    |\n| pullDownToRefreshThreshold | 下拉触发刷新的距离                                              | number  | 100                 | false    |\n| refreshFunction            | 下拉刷新触发的事件                                              | func    | -                   | false    |\n| onScroll                   | 容器滚动时触发的事件                                            | func    | -                   | false    |\n| dataLength                 | 数据的长度                                                      | number  | -                   | false    |\n"},"input-item":{"name":"input-item","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {InputItem, List} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  constructor(props) {\n    super(props);\n    this.handleSexItemClick = this.handleSexItemClick.bind(this);\n    this.handleToggleSexShow = this.handleToggleSexShow.bind(this);\n    this.state = {\n      sexShow: false,\n      sex: '',\n    };\n  }\n\n  handleToggleSexShow() {\n    this.setState({\n      sexShow: !this.state.sexShow,\n    });\n  }\n\n  handleSexItemClick(item, index) {\n    const {\n      input: {onChange},\n    } = this.props;\n    this.setState({\n      sex: item,\n    });\n    this.handleToggleSexShow();\n  }\n  render() {\n    return (\n      <div>\n        <List>\n          <InputItem placeholder={'111'} defaultValue=\"123\" clear extra={<Icon type={'tip'} />}>\n            姓名\n          </InputItem>\n          <InputItem\n            placeholder={'sex'}\n            value={this.state.sex}\n            editable={false}\n            onClick={() => {\n              this.handleToggleSexShow();\n            }}\n          >\n            患者性别\n          </InputItem>\n          <ActionSheet\n            show={this.state.sexShow}\n            onCancel={this.handleToggleSexShow}\n            active={this.state.sex}\n            hasCancel\n            space\n            data={['男', '女']}\n            itemClick={this.handleSexItemClick}\n          />\n        </List>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"输入框","__content":"\n\n输入框，可以带清空按钮\n\n<DEMO>\n\n| 参数        | 说明                               | 类型                          | 默认值                      |\n| ----------- | ---------------------------------- | ----------------------------- | --------------------------- |\n| prefixCls   | 组件 class 前缀                    | string                        | Yep-clearab-clearable-input |\n| style       | 组件样式                           | object                        | {}                          |\n| className   | 组件额外样式                       | string                        | -                           |\n| value       | 输入框的值                         | string                        | -                           |\n| onChange    | 输入改变后的回调                   | string                        | -                           |\n| size        | 输入框大小，除默认外可选值：sm、lg | PropTypes.oneOf(['sm', 'lg']) | -                           |\n| disabled    | 是否禁用                           | bool                          | -                           |\n| placeholder | 同 input placeholder               | string                        | -                           |\n"},"list":{"name":"list","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {List} from '@jdcfe/yep-react';\nconst {Item, Brief} = List;\n\nclass Demo extends React.PureComponent {\n  constructor() {\n    super();\n    this.state = {\n      disabled: false,\n    };\n  }\n\n  render() {\n    return (\n      <div>\n        <List renderHeader={() => 'Basic Style'} className=\"my-list\">\n          <Item extra={'extra content'}>Title</Item>\n          <Item extra={'extra content'}>Title</Item>\n          <Item extra={'extra content'}>Title</Item>\n          <Item extra={'extra content'}>Title</Item>\n        </List>\n        <List renderHeader={() => 'Subtitle'} className=\"my-list\">\n          <Item arrow=\"horizontal\" multipleLine onClick={() => {}}>\n            Title <Brief>subtitle</Brief>\n          </Item>\n          <Item\n            arrow=\"horizontal\"\n            multipleLine\n            onClick={() => {}}\n            icon={'arrow-right'}\n            thumb=\"https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png\"\n          >\n            ListItem （Android）\n          </Item>\n          <Item\n            arrow=\"horizontal\"\n            thumb=\"https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png\"\n            multipleLine\n            icon={\n              <img\n                style={{display: 'block', width: 15, height: 15}}\n                src=\"http://img13.360buyimg.com/uba/jfs/t23977/282/1022509367/2839/74d4afce/5b4dad41N005d5914.png\"\n              />\n            }\n            onClick={() => {}}\n            extra={'extra content'}\n          >\n            Title\n          </Item>\n        </List>\n        <List renderHeader={() => 'Icon in the left'}>\n          <Item\n            thumb=\"https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png\"\n            arrow=\"horizontal\"\n            onClick={() => {}}\n          >\n            My wallet\n          </Item>\n          <Item\n            thumb=\"https://zos.alipayobjects.com/rmsportal/UmbJMbWOejVOpxe.png\"\n            onClick={() => {}}\n            arrow=\"horizontal\"\n          >\n            My Cost Ratio\n          </Item>\n        </List>\n        <List renderHeader={() => 'Text Wrapping'} className=\"my-list\">\n          <Item data-seed=\"logId\">Single line，long text will be hidden with ellipsis；</Item>\n          <Item wrap>\n            Multiple line，long text will wrap；Long Text Long Text Long Text Long Text Long Text Long Text\n          </Item>\n          <Item extra=\"extra content\" multipleLine align=\"top\" wrap>\n            Multiple line and long text will wrap. Long Text Long Text Long Text\n          </Item>\n          <Item extra=\"no arrow\" arrow=\"empty\" className=\"spe\" wrap>\n            In rare cases, the text of right side will wrap in the single line with long text. long text long text long\n            text\n          </Item>\n        </List>\n        <List renderHeader={() => 'Other'} className=\"my-list\">\n          <Item\n            disabled={this.state.disabled}\n            extra=\"\"\n            onClick={() => {\n              console.log('click', this.state.disabled);\n              this.setState({disabled: !this.state.disabled});\n            }}\n          >\n            Click to disable\n          </Item>\n          <Item>\n            <select defaultValue=\"1\">\n              <option value=\"1\">Html select element</option>\n              <option value=\"2\" disabled>\n                Unable to select\n              </option>\n              <option value=\"3\">option 3</option>\n            </select>\n          </Item>\n        </List>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"可输入列表","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {List, Button, Switch} from '@jdcfe/yep-react';\nconst {Item} = List;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <List renderHeader={() => 'Input Style'} className=\"my-list\">\n          <Item extra={<Switch />}>Confirm Infomation</Item>\n          <Item extra={<input />}>Account</Item>\n          <Item>\n            <Button type=\"primary\" size=\"small\" inline onClick={() => alert('Submit')}>\n              Submit\n            </Button>\n            <Button size=\"small\" inline style={{marginLeft: '2.5px'}} onClick={() => alert('Reset')}>\n              Reset\n            </Button>\n          </Item>\n        </List>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"列表","__content":"\n\n单个连续模块垂直排列，显示当前的内容、状态和可进行的操作。eg：联系人列表。\n\n<DEMO>\n\nList\n\n| 参数         | 说明            | 类型      | 默认值   |\n| ------------ | --------------- | --------- | -------- |\n| prefixCls    | 组件 class 前缀 | string    | Yep-list |\n| style        | 组件样式        | object    | {}       |\n| className    | 组件额外样式    | string    | -        |\n| renderHeader | list 的头部     | func/node | -        |\n| renderFooter | list 的底部     | func/node | -        |\n\nList.Item\n\n| 参数         | 说明                                                   | 类型                 | 默认值        |\n| ------------ | ------------------------------------------------------ | -------------------- | ------------- |\n| prefixCls    | 组件 class 前缀                                        | string               | Yep-list-item |\n| style        | 组件样式                                               | object               | {}            |\n| className    | 组件额外样式                                           | string               | -             |\n| thumb        | 缩略图(当为 string 类型时作为 img src)                 | string/node          | -             |\n| extra        | 右边内容                                               | String/React.Element | 无            |\n| icon         | 右侧图标，一般是右箭头 arrow-right，也可以是自定义组件 | String/Node          | 无            |\n| align        | 子元素垂直对齐，可选`top`,`middle`,`bottom`            | String               | `middle`      |\n| onClick      | 点击事件的回调函数                                     | (): void             | 无            |\n| error        | 报错样式,右侧文字颜色变成橙色                          | Boolean              | `false`       |\n| multipleLine | 多行                                                   | Boolean              | `false`       |\n| wrap         | 是否换行，默认情况下，文字超长会被隐藏，               | Boolean              | `false`       |\n| activeStyle  | 自定义 active 的样式                                   | Object               | -             |\n\nList.Brief 辅助说明\n\n| 参数      | 说明            | 类型   | 默认值   |\n| --------- | --------------- | ------ | -------- |\n| prefixCls | 组件 class 前缀 | string | Yep-list |\n| style     | 组件样式        | object | {}       |\n| children  | 辅助说明文本    | string | -        |\n"},"mask":{"name":"mask","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport {Mask, Button, WhiteSpace} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    show: false,\n    transparentShow: false,\n  };\n\n  handleToggleShow = () => {\n    this.setState({\n      show: !this.state.show,\n    });\n  };\n  handleToggleTransparentShow = () => {\n    this.setState({\n      transparentShow: !this.state.transparentShow,\n    });\n  };\n\n  render() {\n    return (\n      <div>\n        <Button type=\"primary\" onClick={this.handleToggleShow}>\n          显示\n        </Button>\n        <WhiteSpace />\n        <Button onClick={this.handleToggleTransparentShow}>显示透明遮罩</Button>\n        <WhiteSpace />\n        {this.state.show && <Mask onClick={this.handleToggleShow} />}\n        {this.state.transparentShow && <Mask transparent onClick={this.handleToggleTransparentShow} />}\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"遮罩层","__content":"\n\n<DEMO>\n\nprops\n\n| 参数        | 说明                  | 类型    | 默认值   |\n| ----------- | --------------------- | ------- | -------- |\n| prefixCls   | 组件 class 前缀       | string  | Yep-mask |\n| style       | 组件样式              | object  | {}       |\n| className   | 额外要添加的 CSS 类名 | string  | -        |\n| transparent | 是否透明              | boolean | false    |\n"},"message":{"name":"message","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Message} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    const aStyle = {\n      display: 'inline-block',\n      border: 'solid 1px #eee',\n      padding: '10px',\n      margin: '10px 10px',\n    };\n    return (\n      <div>\n        <Message tipType=\"error\">电话号码无效</Message>\n        <br />\n        <Message tipType=\"warn\">\n          <p>\n            <b style={{color: 'red'}}>注意：</b>小心熊出没\n          </p>\n          <p>光头强要小心啦！</p>\n        </Message>\n        <Message tipType=\"error\" duration={3000} position=\"fix-top\">\n          顶部通知，3秒后消失\n        </Message>\n        <Message\n          position=\"fix-bottom\"\n          ref={message2 => {\n            this.message2 = message2;\n          }}\n        >\n          底部通知\n        </Message>\n        <a\n          style={aStyle}\n          onClick={() => {\n            this.message2.showMessage();\n          }}\n        >\n          显示底部通知\n        </a>\n        <a\n          style={aStyle}\n          onClick={() => {\n            this.message2.closeMessage();\n          }}\n        >\n          关闭底部通知\n        </a>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"通知","__content":"\n\n弹出通知，包括绝对定位和相对定位，绝对定位直接在视窗顶部底部弹出，相对定位，在组件响应的位置动画弹出\n\n<DEMO>\n\n| 参数      | 说明                                                           | 类型    | 默认值      |\n| --------- | -------------------------------------------------------------- | ------- | ----------- |\n| prefixCls | 组件 class 前缀                                                | string  | Yep-message |\n| style     | 组件样式                                                       | object  | {}          |\n| className | 组件额外样式                                                   | string  | -           |\n| duration  | 通知显示时长，单位为毫秒，-1 代表一直显示                      | number  | -1          |\n| tipType   | 通知类型，内置三种：普通 info，警告 warn，错误 error           | string  | info        |\n| position  | 通知显示位置：顶部 fix-top，底部 fix-bottom，保持不变 in-place | string  | in-place    |\n| icon      | 是否显示通知图标                                               | boolean | false       |\n"},"nav-bar":{"name":"nav-bar","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {NavBar, WhiteSpace, Icon} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <NavBar\n          leftContent=\"返回\"\n          close\n          onCloseClick={() => {\n            console.log('close');\n          }}\n          share={<Icon type=\"share\" />}\n          rightContent={<Icon type=\"lego_gengduo\" />}\n        >\n          页面标题\n        </NavBar>\n        <WhiteSpace />\n        <NavBar leftContent=\"返回\" rightContent={<Icon type=\"lego_gengduo\" />}>\n          页面标题\n        </NavBar>\n        <WhiteSpace />\n        <NavBar leftIcon={null} leftContent=\"取消\" rightContent=\"操作\">\n          页面标题\n        </NavBar>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"导航栏","__content":"\n\n位于 app 内容区的上方，系统状态栏的下方，并且提供在一系列页面中的导航能力。\n\n<DEMO>\n\n| 参数         | 说明                             | 类型        | 默认值                                |\n| ------------ | -------------------------------- | ----------- | ------------------------------------- |\n| prefixCls    | 组件 class 前缀                  | string      | Yep-nav-bar                           |\n| style        | 组件样式                         | object      | {}                                    |\n| className    | 组件额外样式                     | string      | -                                     |\n| leftIcon     | 出现在最左边的图标占位符         | node        | <Icon type=\"arrow-back\" size=\"xs\" />, |\n| leftContent  | 导航左边内容                     | node\\string | -                                     |\n| onLeftClick  | 导航左边点击回调                 | func        | () => null                            |\n| close        | 导航左边内容右边关闭             | string      | -                                     |\n| onCloseClick | 导航左边内容右边关闭组件点击回调 | func        | () => null                            |\n| rightContent | 导航右边内容                     | node\\string | -                                     |\n| onRightClick | 导航右边内容点击回调             | func        | () => null                            |\n| share        | 导航右边内容左侧分享组件         | node        | -                                     |\n"},"picker-view":{"name":"picker-view","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {PickerView} from '@jdcfe/yep-react';\n\nconst seasons = [\n  [\n    {\n      label: '2013',\n      value: '2013',\n    },\n    {\n      label: '2014',\n      value: '2014',\n    },\n  ],\n  [\n    {\n      label: '春',\n      value: '春',\n    },\n    {\n      label: '夏',\n      value: '夏',\n    },\n  ],\n];\nclass Demo extends React.Component {\n  state = {\n    value: null,\n  };\n  onChange = value => {\n    console.log(value);\n    this.setState({\n      value,\n    });\n  };\n  onScrollChange = value => {\n    console.log(value);\n  };\n  render() {\n    return (\n      <PickerView\n        onChange={this.onChange}\n        onScrollChange={this.onScrollChange}\n        value={this.state.value}\n        data={seasons}\n        cascade={false}\n      />\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"选择器","__content":"\n\nPickerView 的功能类似于 Picker ，但它是直接渲染在区域中，而不是弹出窗口。\n\n## API\n\n| 属性            | 说明                                                             | 类型                                                     | 默认值         |\n| --------------- | ---------------------------------------------------------------- | -------------------------------------------------------- | -------------- |\n| data            | 数据源                                                           | `Array<{value, label}>` / `Array<Array<{value, label}>>` | -              |\n| value           | 值, 格式是`[value1, value2, value3]`, 对应数据源的相应级层 value | Array                                                    | -              |\n| cascade         | 是否级联                                                         | Boolean                                                  | true           |\n| cols            | 列数                                                             | Number                                                   | `3`            |\n| onChange        | 选中后的回调                                                     | (val): void                                              | -              |\n| prefixCls       | prefix class                                                     | string                                                   | Yep-picker     |\n| pickerPrefixCls | picker prefix class                                              | string                                                   | Yep-picker-col |\n| itemStyle       | 每列样式                                                         | Object                                                   | -              |\n| indicatorStyle  | indicator 样式                                                   | Object                                                   | -              |\n"},"picker":{"name":"picker","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Picker, Button, WhiteSpace} from '@jdcfe/yep-react';\n\nconst city = [\n  {\n    value: '340000',\n    label: '安徽省',\n    children: [\n      {\n        value: '341500',\n        label: '六安市',\n        children: [\n          {\n            value: '341522',\n            label: '霍邱县',\n            children: [],\n          },\n          {\n            value: '341525',\n            label: '霍山县',\n            children: [],\n          },\n          {\n            value: '341502',\n            label: '金安区',\n            children: [],\n          },\n          {\n            value: '341524',\n            label: '金寨县',\n            children: [],\n          },\n          {\n            value: '341526',\n            label: '其它区',\n            children: [],\n          },\n          {\n            value: '341521',\n            label: '寿县',\n            children: [],\n          },\n          {\n            value: '341523',\n            label: '舒城县',\n            children: [],\n          },\n          {\n            value: '341503',\n            label: '裕安区',\n            children: [],\n          },\n        ],\n      },\n      {\n        value: '340500',\n        label: '马鞍山市',\n        children: [\n          {\n            value: '340506',\n            label: '博望区',\n            children: [],\n          },\n        ],\n      },\n      {\n        value: '341800',\n        label: '宣城市',\n        children: [\n          {\n            value: '341822',\n            label: '广德县',\n            children: [],\n          },\n          {\n            value: '341824',\n            label: '绩溪县',\n            children: [],\n          },\n          {\n            value: '341825',\n            label: '旌德县',\n            children: [],\n          },\n        ],\n      },\n    ],\n  },\n  {\n    value: '820000',\n    label: '澳门特别行政区',\n    children: [\n      {\n        value: '820100',\n        label: '澳门半岛',\n        children: [],\n      },\n      {\n        value: '820200',\n        label: '离岛',\n        children: [],\n      },\n    ],\n  },\n  {\n    value: '110000',\n    label: '北京',\n    children: [\n      {\n        value: '110100',\n        label: '北京市',\n        children: [\n          {\n            value: '110114',\n            label: '昌平区',\n            children: [],\n          },\n          {\n            value: '110105',\n            label: '朝阳区',\n            children: [],\n          },\n          {\n            value: '110103',\n            label: '崇文区',\n            children: [],\n          },\n          {\n            value: '110115',\n            label: '大兴区',\n            children: [],\n          },\n          {\n            value: '110101',\n            label: '东城区',\n            children: [],\n          },\n          {\n            value: '110111',\n            label: '房山区',\n            children: [],\n          },\n          {\n            value: '110106',\n            label: '丰台区',\n            children: [],\n          },\n          {\n            value: '110108',\n            label: '海淀区',\n            children: [],\n          },\n          {\n            value: '110116',\n            label: '怀柔区',\n            children: [],\n          },\n          {\n            value: '110109',\n            label: '门头沟区',\n            children: [],\n          },\n          {\n            value: '110228',\n            label: '密云县',\n            children: [],\n          },\n          {\n            value: '110117',\n            label: '平谷区',\n            children: [],\n          },\n          {\n            value: '110230',\n            label: '其它区',\n            children: [],\n          },\n          {\n            value: '110107',\n            label: '石景山区',\n            children: [],\n          },\n          {\n            value: '110113',\n            label: '顺义区',\n            children: [],\n          },\n          {\n            value: '110112',\n            label: '通州区',\n            children: [],\n          },\n          {\n            value: '110102',\n            label: '西城区',\n            children: [],\n          },\n          {\n            value: '110104',\n            label: '宣武区',\n            children: [],\n          },\n          {\n            value: '110229',\n            label: '延庆县',\n            children: [],\n          },\n        ],\n      },\n    ],\n  },\n];\n\nclass Demo extends React.PureComponent {\n  constructor() {\n    super();\n    this.handleToggleShow = this.handleToggleShow.bind(this);\n    this.handleToggleSlideDownShow = this.handleToggleSlideDownShow.bind(this);\n    this.handleToggleSlideRightShow = this.handleToggleSlideRightShow.bind(this);\n    this.state = {\n      show: false,\n    };\n  }\n\n  handleToggleShow() {\n    this.setState({\n      show: !this.state.show,\n    });\n  }\n\n  handleToggleSlideDownShow() {\n    this.setState({\n      slideDownShow: !this.state.slideDownShow,\n    });\n  }\n\n  handleToggleSlideRightShow() {\n    this.setState({\n      slideRightShow: !this.state.slideRightShow,\n    });\n  }\n\n  render() {\n    return (\n      <div title=\"Picker\">\n        <Button type=\"primary\" onClick={this.handleToggleShow}>\n          选择医生\n        </Button>\n        <WhiteSpace />\n        <Button onClick={this.handleToggleSlideDownShow}>选择地区</Button>\n        <WhiteSpace />\n        <Button type=\"primary\" onClick={this.handleToggleSlideRightShow}>\n          选择婚姻状况\n        </Button>\n\n        <Picker\n          cols={1}\n          show={this.state.show}\n          onCancel={this.handleToggleShow}\n          onOk={value => {\n            this.handleToggleShow();\n            alert(value);\n          }}\n          data={[\n            {value: '住院医师', label: '住院医师'},\n            {value: '主治医师', label: '主治医师'},\n            {value: '副主任医师', label: '副主任医师'},\n            {value: '主任医师', label: '住院医师'},\n          ]}\n        />\n\n        <Picker\n          show={this.state.slideDownShow}\n          onCancel={this.handleToggleSlideDownShow}\n          hasCancel\n          space\n          data={city}\n          onChange={v => {\n            this.setState({pickerValue: v});\n            console.log(v);\n          }}\n          onOk={value => {\n            console.log(value);\n            this.handleToggleSlideDownShow();\n          }}\n        />\n        <Picker\n          show={this.state.slideRightShow}\n          onCancel={this.handleToggleSlideRightShow}\n          hasCancel\n          space\n          cols={1}\n          title=\"婚姻状况\"\n          onOk={value => {\n            alert(value);\n            this.handleToggleSlideRightShow();\n          }}\n          data={[{value: '未婚', label: '未婚'}, {value: '已婚', label: '已婚'}, {value: '离异', label: '离异'}]}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"选择器","__content":"\n\n<DEMO>\n\n| 参数                 | 说明                                                             | 类型                                     | 默认值     |\n| -------------------- | ---------------------------------------------------------------- | ---------------------------------------- | ---------- |\n| prefixCls            | 组件 class 前缀                                                  | string                                   | Yep-picker |\n| style                | 组件样式                                                         | object                                   | {}         |\n| show                 | 是否显示                                                         | boolean                                  | false      |\n| onCancel             | 点击取消时执行的回调                                             | func                                     | ()=>{}     |\n| onOk                 | 点击选中时执行的回调                                             | func                                     | ()=>{}     |\n| data                 | 数据源                                                           | `Array<{value, label, children: Array}>` | -          |\n| defaultSelectedValue | 默认选中的数据                                                   | string，number                           | -          |\n| value                | 值, 格式是`[value1, value2, value3]`, 对应数据源的相应级层 value | Array                                    | -          |\n| cols                 | 列数                                                             | Number                                   | `3`        |\n| onChange             | 选中后的回调                                                     | (val): void                              | -          |\n| onPickerChange       | 每列数据选择变化后的回调函数                                     | (val): void                              | -          |\n| itemStyle            | 每列样式                                                         | Object                                   | -          |\n| indicatorStyle       | indicator 样式                                                   | Object                                   | -          |\n| onOk                 | 点击选中时执行的回调                                             | (val): void                              | 无         |\n| title                | 大标题                                                           | String                                   | -          |\n| disabled             | 是否不可用                                                       | Boolean                                  | false      |\n| cascade              | 是否联动                                                         | Boolean                                  | true       |\n"},"popup":{"name":"popup","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Popup, Button, WhiteSpace} from '@jdcfe/yep-react';\nclass Demo extends React.PureComponent {\n  constructor() {\n    super();\n    this.handleToggleShow = this.handleToggleShow.bind(this);\n    this.handleToggleSlideDownShow = this.handleToggleSlideDownShow.bind(this);\n    this.handleToggleSlideRightShow = this.handleToggleSlideRightShow.bind(this);\n    this.state = {\n      show: false,\n      slideDownShow: false,\n      slideRightShow: false,\n    };\n  }\n\n  handleToggleShow() {\n    this.setState({\n      show: !this.state.show,\n    });\n  }\n\n  handleToggleSlideDownShow() {\n    this.setState({\n      slideDownShow: !this.state.slideDownShow,\n    });\n  }\n  handleToggleSlideRightShow() {\n    this.setState({\n      slideRightShow: !this.state.slideRightShow,\n    });\n  }\n\n  render() {\n    return (\n      <div title=\"Popup\">\n        <Button type=\"primary\" onClick={this.handleToggleShow}>\n          显示\n        </Button>\n        <WhiteSpace />\n        <Button onClick={this.handleToggleSlideDownShow}>从上向下显示</Button>\n        <WhiteSpace />\n        <Button type=\"primary\" onClick={this.handleToggleSlideRightShow}>\n          从右向左显示\n        </Button>\n\n        <Popup show={this.state.show} onCancel={this.handleToggleShow}>\n          <div className=\"ul\">\n            <div\n              className=\"cur\"\n              onClick={() => {\n                alert('未婚');\n              }}\n            >\n              未婚\n            </div>\n            <div\n              className=\"\"\n              onClick={() => {\n                alert('已婚');\n              }}\n            >\n              已婚\n            </div>\n            <div\n              className=\"\"\n              onClick={() => {\n                alert('离异');\n              }}\n            >\n              离异\n            </div>\n          </div>\n        </Popup>\n\n        <Popup show={this.state.slideDownShow} onCancel={this.handleToggleSlideDownShow} popupTransition=\"slide-down\">\n          <div className=\"up\">\n            <div\n              className=\"cur\"\n              onClick={() => {\n                alert('未婚');\n              }}\n            >\n              未婚\n            </div>\n          </div>\n        </Popup>\n        <Popup\n          show={this.state.slideRightShow}\n          onCancel={this.handleToggleSlideRightShow}\n          popupTransition=\"slide-right\"\n        >\n          <div className=\"right\">\n            <div\n              className=\"cur\"\n              onClick={() => {\n                alert('未婚');\n              }}\n            >\n              未婚\n            </div>\n          </div>\n        </Popup>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"style":".ul {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  transition: all .2s;\n  background-color: #fff;\n}\n\n.ul div {\n  line-height: .95rem;\n  font-size: .28rem;\n  text-align: center;\n  color: #333;\n  position: relative;\n  padding: 0 .2rem;\n}\n\n.ul div .cur {\n  color: #f12222;\n}\n\n.ul div :before {\n  content: \"\";\n  display: block;\n  position: absolute;\n  width: 100%;\n  left: 0;\n  bottom: 0;\n  height: .01rem;\n  background-color: #dedede;\n  -webkit-transform: scaleY(.5);\n  transform: scaleY(.5);\n  pointer-events: none;\n}\n\n.up {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  transition: all .2s;\n  background-color: #000;\n}\n\n.up div {\n  line-height: .95rem;\n  font-size: .28rem;\n  text-align: center;\n  color: #fff;\n  position: relative;\n  padding: 0 .2rem;\n}\n\n.up div.cur {\n  color: #f12222;\n}\n\n.up div:before {\n  content: \"\";\n  display: block;\n  position: absolute;\n  width: 100%;\n  left: 0;\n  bottom: 0;\n  height: .01rem;\n  background-color: #dedede;\n  -webkit-transform: scaleY(.5);\n  transform: scaleY(.5);\n  pointer-events: none;\n}\n\n.right {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  width: 90%;\n  transition: all .2s;\n  background-color: #fff;\n}\n\n.right div {\n  line-height: .95rem;\n  font-size: .28rem;\n  text-align: center;\n  color: #fff;\n  position: relative;\n  padding: 0 .2rem;\n}\n\n.right div.cur {\n  color: #f12222;\n}\n\n.right div:before {\n  content: \"\";\n  display: block;\n  position: absolute;\n  width: 100%;\n  left: 0;\n  bottom: 0;\n  height: .01rem;\n  background-color: #dedede;\n  -webkit-transform: scaleY(.5);\n  transform: scaleY(.5);\n  pointer-events: none;\n}\n","category":"Components","title":"弹出框","__content":"\n\n作为 Picker 和 ActionSheet 组件的容器组件实现\n\n<DEMO>\n\n| 参数            | 说明                                                                        | 类型    | 默认值     |\n| --------------- | --------------------------------------------------------------------------- | ------- | ---------- |\n| prefixCls       | 组件 class 前缀                                                             | string  | Yep-picker |\n| style           | 组件样式                                                                    | object  | {}         |\n| show            | 是否显示                                                                    | boolean | false      |\n| onCancel        | 点击取消时执行的回调                                                        | func    | ()=>{}     |\n| popupTransition | popup 弹出动画，默认提供 slide-up、slide-down、slide-right，默认为 slide-up | string  | slide-up   |\n| maskTransition  | mask 层的动画，默认为 fade                                                  | string  | fade       |\n| maskCloseable   | 点击蒙层是否关闭                                                            | boolean | true       |\n"},"progress-bar":{"name":"progress-bar","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {ProgressBar, WhiteSpace, Button} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    percent: 50,\n  };\n\n  add = () => {\n    let p = this.state.percent + 10;\n    if (this.state.percent >= 100) {\n      p = 0;\n    }\n    this.setState({percent: p});\n  };\n\n  render() {\n    return (\n      <div title=\"ProgressBar\">\n        <ProgressBar percent={150} />\n        <WhiteSpace />\n        <ProgressBar percent={this.state.percent} backgroundColor=\"#000\" />\n        <WhiteSpace />\n        <ProgressBar percent={60} colorStart=\"#feaa36\" colorEnd=\"#ff5741\" />\n        <WhiteSpace />\n        <ProgressBar percent={this.state.percent} fixed />\n        <Button onClick={this.add}>(+-)10</Button>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"进度条","__content":"\n\n<DEMO>\n\n| 参数            | 说明                                               | 类型    | 默认值                 |\n| --------------- | -------------------------------------------------- | ------- | ---------------------- |\n| prefixCls       | 组件 class 前缀                                    | string  | Yep-common-progressbar |\n| style           | 组件样式                                           | object  | {}                     |\n| className       | 组件额外 class                                     | string  | {}                     |\n| percent         | 进度条的值                                         | number  | 0                      |\n| colorStart      | 进度条渐变颜色的起始值                             | string  | -                      |\n| colorEnd        | 进度条渐变颜色的结束值                             | string  | -                      |\n| backgroundColor | 进度条背景颜色(与 colorStart、colorEnd 只能取一个) | string  | #ec3838                |\n| fixed           | 是否固定在顶部                                     | boolean | false                  |\n"},"radio":{"name":"radio","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Radio} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  constructor() {\n    super();\n\n    this.state = {\n      options1: ['Option1', 'Option2'],\n      options2: [\n        {\n          label: 'Option1',\n          value: '1',\n        },\n        {\n          label: 'Option2',\n          value: '2',\n          disabled: true,\n        },\n        {\n          label: 'Option3',\n          value: '3',\n        },\n      ],\n    };\n  }\n\n  render() {\n    return (\n      <div title=\"Radio\">\n        <Radio options={this.state.options1} name=\"eat\" />\n        <Radio options={this.state.options2} horizontal />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"单选框","__content":"\n\n<DEMO>\n\n| 参数       | 说明            | 类型                                                                      | 默认值    |\n| ---------- | --------------- | ------------------------------------------------------------------------- | --------- |\n| prefixCls  | 组件 class 前缀 | string                                                                    | Yep-radio |\n| style      | 组件样式        | object                                                                    | {}        |\n| className  | 额外 class      | string                                                                    | -         |\n| name       | name            | string                                                                    | -         |\n| options    | 选项            | Array [{label: 'Option3',value: '3',disabled: true,defaultChecked:true,}] | []        |\n| checked    | 选中项的 value  | string                                                                    | -         |\n| horizontal | 是否水平排列    | boolean                                                                   | false     |\n"},"rate":{"name":"rate","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Rate} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    rating: 4,\n  };\n\n  onStarClick = (nextValue, prevValue, name) => {\n    this.setState({rating: nextValue});\n  };\n\n  render() {\n    const {rating} = this.state;\n    return (\n      <div>\n        <Rate name=\"rate1\" starCount={10} value={rating} onStarClick={this.onStarClick} />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":3,"title":"自定义半星图标","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Rate} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    rating: 5.5,\n  };\n\n  onStarClick = (nextValue, prevValue, name) => {\n    this.setState({rating: nextValue});\n  };\n\n  render() {\n    const {rating} = this.state;\n    return (\n      <div>\n        <Rate\n          name=\"rate1\"\n          starCount={10}\n          renderStarIconHalf={(index, value, name, id) => <Icon type=\"star-half-empty\" key={`icon_${id}`} />}\n          value={rating}\n          onStarClick={this.onStarClick}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":2,"title":"自定义图标","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Rate} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    rating: 6,\n  };\n\n  onStarClick = (nextValue, prevValue, name) => {\n    this.setState({rating: nextValue});\n  };\n\n  render() {\n    const {rating} = this.state;\n    return (\n      <div>\n        <Rate\n          className=\"custom\"\n          name=\"rate1\"\n          starCount={10}\n          renderStarIcon={(index, value, name, id) => (\n            <i key={`icon_${id}`} style={{fontStyle: 'normal', fontSize: 30}}>\n              &#9733;\n            </i>\n          )}\n          value={rating}\n          onStarClick={this.onStarClick}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"不可编辑","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Rate} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    rating: 8,\n  };\n\n  onStarClick = (nextValue, prevValue, name) => {\n    this.setState({rating: nextValue});\n  };\n\n  render() {\n    const {rating} = this.state;\n    return (\n      <div>\n        <Rate name=\"rate1\" editing={false} starCount={10} value={rating} onStarClick={this.onStarClick} />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"style":".custom .Yep-rate-empty-star {\n  color: #333;\n}\n","category":"Components","title":"评分","__content":"\n\n评分组件，可以定制\n\n<DEMO>\n\n| 参数               | 说明             | 类型                          | 默认值   |\n| ------------------ | ---------------- | ----------------------------- | -------- |\n| prefixCls          | 组件 class 前缀  | string                        | Yep-rate |\n| style              | 组件样式         | object                        | {}       |\n| className          | 组件额外样式     | string                        | -        |\n| name               | input name       | string                        | -        |\n| value              | 初始值           | number                        | -        |\n| editing            | 是否可改变       | boolean                       | true     |\n| starCount          | star 的数量      | number                        | 5        |\n| onStarClick        | star 点击回调    | func                          | -        |\n| renderStarIcon     | star icon 自定义 | （index, value, name, id） => | -        |\n| renderStarIconHalf | 自定义半星       | （index, value, name, id） => | -        |\n"},"scratch":{"name":"scratch","style":".scratch-child {\n    line-height: 60px;\n    text-align: center;\n    background-color: #f8f3da;\n    color: #ff001e;\n}","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Scratch} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Scratch\n          scratchImgUrl={\n            'http://img13.360buyimg.com/uba/jfs/t18799/156/2627259082/17162/d0d22282/5b027b37N5b0fa969.png'\n          }\n          resPercent={50}\n          resCallback={() => {\n            alert('中奖了！！！');\n          }}\n          children={<div className=\"scratch-child\">中奖了</div>}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"刮奖","__content":"\n\n<DEMO>\n\n| 参数          | 说明                                     | 类型       | 默认值      |\n| ------------- | ---------------------------------------- | ---------- | ----------- |\n| prefixCls     | 组件 class 前缀                          | string     | Yep-scratch |\n| style         | 组件样式                                 | object     | {}          |\n| className     | 组件额外样式                             | string     | -           |\n| scratchImgUrl | 刮奖层图片 url                           | string     | -           |\n| resPercent    | 刮开至整个刮奖区域的该百分比时，刮层消失 | number     | 60          |\n| resCallback   | 刮层消失时的回调函数                     | func       | ()=>{}      |\n| children      | 开奖层内容                               | React.Node | -           |\n"},"search-bar":{"name":"search-bar","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {SearchBar} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    value: '',\n  };\n  render() {\n    return (\n      <div>\n        <SearchBar\n          onSearch={() => console.log('跳搜索结果页面')}\n          clearable={true}\n          placeholder={'请输入您要搜索的商品'}\n          value={this.state.value}\n          onChange={value => this.setState({value})}\n        />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"搜索","__content":"\n\n搜索\n\n<DEMO>\n\n| 参数        | 说明                 | 类型     | 默认值         |\n| ----------- | -------------------- | -------- | -------------- |\n| prefixCls   | 组件 class 前缀      | string   | Yep-search-bar |\n| style       | 组件样式             | object   | {}             |\n| className   | 组件额外样式         | string   | -              |\n| value       | 输入框的值           | string   | -              |\n| onChange    | 输入改变后的回调     | function | -              |\n| disabled    | 是否禁用             | bool     | -              |\n| placeholder | 同 input placeholder | string   | -              |\n| onSearch    | 搜索触发后的回调     | function | -              |\n"},"skeleton":{"name":"skeleton","demos":[{"order":4,"title":"BulletList Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\nconst {BulletListStyle} = Skeleton;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <BulletListStyle />\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":2,"title":"Code Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\nconst {CodeStyle} = Skeleton;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <CodeStyle />\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":5,"title":"Custom Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <Skeleton height={140} speed={1} primaryColor={'#333'} secondaryColor={'#999'}>\n          {/* Pure SVG */}\n          <rect x=\"0\" y=\"0\" rx=\"5\" ry=\"5\" width=\"70\" height=\"70\" />\n          <rect x=\"80\" y=\"17\" rx=\"4\" ry=\"4\" width=\"300\" height=\"13\" />\n          <rect x=\"80\" y=\"40\" rx=\"3\" ry=\"3\" width=\"250\" height=\"10\" />\n        </Skeleton>\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":0,"title":"Facebook Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\nconst {FacebookStyle} = Skeleton;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <FacebookStyle />\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"Instagram Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\nconst {InstagramStyle} = Skeleton;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <InstagramStyle />\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":3,"title":"List Style","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Skeleton} from '@jdcfe/yep-react';\nconst {ListStyle} = Skeleton;\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <ListStyle />\n      </div>\n    );\n  }\n}\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"占位符","__content":"\n\n使用 SVG 创建一个模拟将被加载的内容结构的加载器集合，类似于 Facebook 卡片加载器\n\n## 特性\n\n* **完全可定制:** 你可以改变颜色，速度和尺寸;\n* **创建你自己的 loading:** 使用\n  [create-content-loader](https://danilowoz.github.io/create-content-loader/) 去创建定制的 loadings;\n* **开箱可用:** FacebookStyle、InstagramStyle、CodeStyle、ListStyle、BulletListStyle;\n* **高性能:** 纯 SVG, 无需 scripts, canvas, etc;\n\n<DEMO>\n\n| 参数                    | 类型        | 默认值           | 说明                                                                      |\n| ----------------------- | ----------- | ---------------- | ------------------------------------------------------------------------- |\n| **animate**             | `{Boolean}` | `true`           | `false` to render with no animation                                       |\n| **speed**               | `{Number}`  | `2`              | Animation speed in seconds                                                |\n| **className**           | `{String}`  | `''`             | Classname in the `<svg/>`                                                 |\n| **width**               | `{Number}`  | `400`            | viewBox width of `<svg/>`                                                 |\n| **height**              | `{Number}`  | `130`            | viewBox height of `<svg/>`                                                |\n| **preserveAspectRatio** | `{String}`  | `xMidYMid meet`  | Aspect ratio option of `<svg/>`                                           |\n| **primaryColor**        | `{String}`  | `#f3f3f3`        | Background                                                                |\n| **secondaryColor**      | `{String}`  | `#ecebeb`        | Animation color                                                           |\n| **primaryOpacity**      | `{Number}`  | `1`              | Background opacity (0 = transparent, 1 = opaque)                          |\n| **secondaryOpacity**    | `{Number}`  | `1`              | Animation opacity (0 = transparent, 1 = opaque)                           |\n| **style**               | `{Object}`  | `null`           | Ex: `{ width: '100%', height: '70px' }`                                   |\n| **uniquekey**           | `{String}`  | random unique id | Use the same value of prop key, that will solve inconsistency on the SSR. |\n"},"sticky":{"name":"sticky","style":"h1 {\n  line-height: 2rem;\n  display: inline-block;\n}\nh2 {\n  line-height: 1.5rem;\n  display: inline-block;\n}\n.app {\n  display: grid;\n  grid-template-columns: 20% 80%;\n}\n@media (max-width: 700px) {\n  .app {\n    grid-template-columns: unset;\n  }\n}\n.navbar {\n  padding: .5rem;\n}\n.navbar .nav-link {\n  padding: .5rem;\n}\n.sticky-header {\n  height: 80px;\n  overflow: auto;\n  background: #aaa;\n}\n.container {\n  height: 500px;\n  background: #ddd;\n}\n.gap {\n  height: 500px;\n}\n.gap.short {\n  height: 250px;\n}\n.gap.tall {\n  height: 1000px;\n}\n.container.relative {\n  overflow-y: auto;\n}\n","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\n\nimport {Sticky} from '@jdcfe/yep-react';\nconst {StickyContainer} = Sticky;\n\nclass Header extends React.Component {\n  static defaultProps = {\n    className: '',\n  };\n  render() {\n    const {style, renderCount, className} = this.props;\n    return (\n      <div className={'sticky-header ' + className} style={style}>\n        <h2>\n          <span className=\"pull-left\">\n            {'<Sticky /> '}\n            {renderCount ? <small>(invocation: #{renderCount})</small> : null}\n          </span>\n        </h2>\n      </div>\n    );\n  }\n}\n\nlet renderCount = 0;\nclass Basic extends React.Component {\n  render() {\n    return (\n      <div>\n        <h2>Content before the Sticky...</h2>\n        <div className=\"gap short\" style={{background: 'linear-gradient(#fff, #ddd)'}} />\n        <StickyContainer className=\"container\">\n          <Sticky>{({style}) => <Header style={style} renderCount={renderCount++} />}</Sticky>\n\n          <h2 className=\"text-center\">{'<StickyContainer />'}</h2>\n        </StickyContainer>\n        <div className=\"gap tall\" style={{background: 'linear-gradient(#ddd, #fff)'}}>\n          <h2>Content after the Sticky...</h2>\n        </div>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Basic />, mountNode);\n```\n"},{"order":1,"title":"Relative","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\n\nimport {Sticky} from '@jdcfe/yep-react';\nconst {StickyContainer} = Sticky;\n\nclass Header extends React.Component {\n  static defaultProps = {\n    className: '',\n  };\n  render() {\n    const {style, renderCount, className} = this.props;\n    return (\n      <div className={'sticky-header ' + className} style={style}>\n        <h2>\n          <span className=\"pull-left\">\n            {'<Sticky /> '}\n            {renderCount ? <small>(invocation: #{renderCount})</small> : null}\n          </span>\n        </h2>\n      </div>\n    );\n  }\n}\n\nlet renderCount = 0;\nclass Relative extends React.Component {\n  render() {\n    return (\n      <div>\n        <StickyContainer className=\"container relative\">\n          <div className=\"gap tall\" style={{background: 'linear-gradient(#aaa, #fff)'}}>\n            <div className=\"gap short\" />\n            <Sticky relative={true}>{({style}) => <Header style={style} renderCount={renderCount++} />}</Sticky>\n            <div className=\"gap short\" />\n            <h2>scrolling container</h2>\n          </div>\n        </StickyContainer>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Relative />, mountNode);\n```\n"},{"order":2,"title":"Stacked","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport ReactDOM from 'react-dom';\n\nimport {Sticky} from '@jdcfe/yep-react';\nconst {StickyContainer} = Sticky;\n\nconst containerBg = i => `hsl(${i * 40}, 70%, 90%)`;\nconst headerBg = i => `hsl(${i * 40}, 70%, 50%)`;\n\nclass Header extends React.Component {\n  static defaultProps = {\n    className: '',\n  };\n  render() {\n    const {style, renderCount, className} = this.props;\n    return (\n      <div className={'sticky-header ' + className} style={style}>\n        <h2>\n          <span className=\"pull-left\">\n            {'<Sticky /> '}\n            {renderCount ? <small>(invocation: #{renderCount})</small> : null}\n          </span>\n        </h2>\n      </div>\n    );\n  }\n}\n\nclass Stacked extends React.Component {\n  render() {\n    return (\n      <div>\n        {[1, 2, 3, 4, 5, 6, 7, 8].map(i => (\n          <StickyContainer key={i} className=\"container\" style={{background: containerBg(i)}}>\n            <Sticky>{({style}) => <Header style={{...style, background: headerBg(i)}} />}</Sticky>\n            <h2 className=\"text-center\">{`<StickyContainer #${i} />`}</h2>\n          </StickyContainer>\n        ))}\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Stacked />, mountNode);\n```\n"}],"category":"Components","title":"吸顶","__content":"\n\nMake your React components sticky!\n\n<DEMO>\n\n## API\n\n`<Sticky />` elements require a function that returns a React component or DOM node as the only child. When invoked, this child function will be supplied an object with the following properties:\n\n- `isSticky` _(boolean)_ - is the element sticky as a result of the current event?\n- `wasSticky` _(boolean)_ - was the element sticky prior to the current event?\n- `distanceFromTop` _(number)_ - number of pixels from the top of the `Sticky` to the nearest `StickyContainer`'s top\n- `distanceFromBottom` _(number)_ - number of pixels from the bottom of the `Sticky` to the nearest `StickyContainer`'s bottom\n- `calculatedHeight` _(number)_ - height of the element returned by this function\n- `style` _(object)_ - modifiable style attributes to optionally be passed to the element returned by this function\n\nThe `Sticky`'s child function will be called when events occur in the parent `StickyContainer`,\nand will serve as the callback to apply your own logic and customizations, with sane `style` attributes\nto get you up and running quickly.\n\napp.js\n\n```js\nimport React from 'react';\nimport { StickyContainer, Sticky } from 'react-sticky';\n...\n\nclass App extends React.Component ({\n  render() {\n    return (\n      ...\n      <StickyContainer>\n        ...\n        <Sticky>\n          {\n            ({\n              style,\n\n              // the following are also available but unused in this example\n              isSticky,\n              wasSticky,\n              distanceFromTop,\n              distanceFromBottom,\n              calculatedHeight\n            }) => {\n              return (\n                <header style={style}>\n                  ...\n                </header>\n              )\n            }\n          }\n        </Sticky>\n        ...\n      </StickyContainer>\n      ...\n    );\n  },\n});\n```\n\nWhen the \"stickiness\" becomes activated, the arguments to the sticky function\nare modified. Similarly, when deactivated, the arguments will update accordingly.\n\n### `<StickyContainer />` Props\n\n`<StickyContainer />` supports all valid `<div />` props.\n\n### `<Sticky />` Props\n\n#### relative _(default: false)_\n\nSet `relative` to `true` if the `<Sticky />` element will be rendered within\nan overflowing `<StickyContainer />` (e.g. `style={{ overflowY: 'auto' }}`) and you want\nthe `<Sticky />` behavior to react to events only within that container.\n\nWhen in `relative` mode, `window` events will not trigger sticky state changes. Only scrolling\nwithin the nearest `StickyContainer` can trigger sticky state changes.\n\n#### topOffset _(default: 0)_\n\nSticky state will be triggered when the top of the element is `topOffset` pixels from the top of the closest `<StickyContainer />`. Positive numbers give the impression of a lazy sticky state, whereas negative numbers are more eager in their attachment.\n\napp.js\n\n```js\n<StickyContainer>\n  ...\n  <Sticky topOffset={80}>\n    { props => (...) }\n  </Sticky>\n  ...\n</StickyContainer>\n```\n\nThe above would result in an element that becomes sticky once its top is greater than or equal to 80px away from the top of the `<StickyContainer />`.\n\n#### bottomOffset _(default: 0)_\n\nSticky state will be triggered when the bottom of the element is `bottomOffset` pixels from the bottom of the closest `<StickyContainer />`.\n\napp.js\n\n```js\n<StickyContainer>\n  ...\n  <Sticky bottomOffset={80}>\n    { props => (...) }\n  </Sticky>\n  ...\n</StickyContainer>\n```\n\nThe above would result in an element that ceases to be sticky once its bottom is 80px away from the bottom of the `<StickyContainer />`.\n\n#### disableCompensation _(default: false)_\n\nSet `disableCompensation` to `true` if you do not want your `<Sticky />` to apply padding to\na hidden placeholder `<div />` to correct \"jumpiness\" as attachment changes from `position:fixed`\nand back.\n\napp.js\n\n```js\n<StickyContainer>\n  ...\n  <Sticky disableCompensation>\n    { props => (...) }\n  </Sticky>\n  ...\n</StickyContainer>\n```\n\n#### disableHardwareAcceleration _(default: false)_\n\nWhen `disableHardwareAcceleration` is set to `true`, the `<Sticky />` element will not use hardware acceleration (e.g. `transform: translateZ(0)`). This setting is not recommended as it negatively impacts\nthe mobile experience, and can usually be avoided by improving the structure of your DOM.\n\napp.js\n\n```js\n<StickyContainer>\n  ...\n  <Sticky disableHardwareAcceleration>\n    { props => (...) }\n  </Sticky>\n  ...\n</StickyContainer>\n```\n"},"swipe-action":{"name":"swipe-action","style":".demo-btn .Yep-swipe-btn-text {\n  width: 1rem;\n  white-space: normal;\n  text-align: center;\n}\n","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {SwipeAction, List} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <SwipeAction\n          style={{backgroundColor: 'gray'}}\n          autoClose\n          right={[\n            {\n              text: '置顶商品',\n              className: 'demo-btn',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#ddd', color: 'white'},\n            },\n            {\n              text: '取消关注',\n              className: 'demo-btn',\n              onClick: () => console.log('delete'),\n              style: {backgroundColor: '#F4333C', color: 'white'},\n            },\n          ]}\n          left={[\n            {\n              text: '提交',\n              onClick: () => console.log('reply'),\n              style: {backgroundColor: '#108ee9', color: 'white'},\n            },\n            {\n              text: '取消',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#ddd', color: 'white'},\n            },\n          ]}\n          onOpen={() => console.log('global open')}\n          onClose={() => console.log('global close')}\n          onMovingDistance={distance => console.log(distance)}\n        >\n          <List.Item extra=\"滑\" arrow=\"horizontal\" onClick={e => console.log(e)}>\n            左右两侧都有按钮\n          </List.Item>\n        </SwipeAction>\n        <SwipeAction\n          style={{backgroundColor: 'gray'}}\n          autoClose\n          right={[\n            {\n              text: '取消',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#ddd', color: 'white'},\n            },\n            {\n              text: '删除',\n              onClick: () => console.log('delete'),\n              style: {backgroundColor: '#F4333C', color: 'white'},\n            },\n          ]}\n          onOpen={() => console.log('global open')}\n          onClose={() => console.log('global close')}\n        >\n          <List.Item extra=\"滑\" arrow=\"horizontal\" onClick={e => console.log(e)}>\n            只有右侧有按钮\n          </List.Item>\n        </SwipeAction>\n        <SwipeAction\n          style={{backgroundColor: 'gray'}}\n          autoClose\n          left={[\n            {\n              text: '提交',\n              onClick: () => console.log('reply'),\n              style: {backgroundColor: '#F4333C', color: 'white'},\n            },\n            {\n              text: '取消',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#ddd', color: 'white'},\n            },\n          ]}\n          onOpen={() => console.log('global open')}\n          onClose={() => console.log('global close')}\n        >\n          <List.Item extra=\"滑\" arrow=\"horizontal\" onClick={e => console.log(e)}>\n            只有左侧有按钮\n          </List.Item>\n        </SwipeAction>\n        <SwipeAction\n          style={{backgroundColor: 'gray'}}\n          autoClose\n          right={[\n            {\n              text: '短',\n              onClick: () => console.log('reply'),\n              style: {backgroundColor: '#ddd', color: 'white'},\n            },\n            {\n              text: '长按钮',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#F4333C', color: 'white'},\n            },\n          ]}\n          onOpen={() => console.log('global open')}\n          onClose={() => console.log('global close')}\n        >\n          <List.Item extra=\"滑\" arrow=\"horizontal\" onClick={e => console.log(e)}>\n            不同宽度的按钮\n          </List.Item>\n        </SwipeAction>\n        <SwipeAction\n          style={{backgroundColor: 'gray'}}\n          autoClose\n          right={[\n            {\n              text: '一个按钮',\n              onClick: () => console.log('cancel'),\n              style: {backgroundColor: '#ec3838', color: 'white'},\n            },\n          ]}\n          onOpen={() => console.log('global open')}\n          onClose={() => console.log('global close')}\n        >\n          <List.Item extra=\"滑\" arrow=\"horizontal\" onClick={e => console.log(e)}>\n            只有一个按钮\n          </List.Item>\n        </SwipeAction>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"},{"order":1,"title":"动态改变","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {SwipeAction, List} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    default: 1,\n  };\n\n  render() {\n    return (\n      <div>\n        {new Array(1).fill(0).map((address, index) => (\n          <SwipeAction\n            autoClose\n            right={\n              this.state.default !== index\n                ? [\n                    {\n                      text: '设为默认',\n                      className: 'demo-btn',\n                      onClick: () => {\n                        this.setState({default: index});\n                      },\n                      style: {backgroundColor: '#ddd', color: 'white'},\n                    },\n                  ]\n                : []\n            }\n          >\n            <div style={{padding: '0.14rem', fontSize: '0.32rem'}}>地址</div>\n          </SwipeAction>\n        ))}\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"滑动操作","__content":"\n\n滑动操作组件\n\n<DEMO>\n\n### SwipeAction Props\n\n| 参数             | 说明                                             | 类型    | 默认值           |\n| ---------------- | ------------------------------------------------ | ------- | ---------------- |\n| prefixCls        | 组件 class 前缀                                  | string  | Yep-swipe        |\n| style            | 组件样式                                         | object  | {}               |\n| className        | 组件额外样式                                     | string  | -                |\n| left             | 左侧按钮组                                       | array   | -                |\n| right            | 右侧按钮组                                       | array   | -                |\n| autoClose        | 点击按钮后自动隐藏按钮                           | boolean | false            |\n| disabled         | 禁用 swipe                                       | boolean | false            |\n| onOpen           | 打开时回调函数                                   | func    | () =>{}          |\n| onClose          | 关闭时回调函数                                   | func    | () =>{}‘         |\n| onMovingDistance | 滑动时回调函数，返回滑动的距离，左滑为-，右滑为+ | func    | (distance) =>{}‘ |\n\n### Button 按钮 Props\n\n| 参数      | 说明         | 类型   | 默认值  |\n| --------- | ------------ | ------ | ------- |\n| text      | 按钮文案     | String | -       |\n| style     | 按钮样式     | Object | {}      |\n| onClick   | 按钮点击事件 | func   | () =>{} |\n| className | 按钮样式类   | String | -       |\n"},"switch":{"name":"switch","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Switch} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  state = {\n    checked: false,\n    checked1: true,\n    checked2: true,\n    checked3: false,\n  };\n\n  render() {\n    return (\n      <div>\n        <Switch currentStatus={this.state.checked} handleSwitch={checked => this.setState({checked})} />\n        <Switch currentStatus={this.state.checked1} handleSwitch={checked => this.setState({checked1: checked})} />\n        <Switch currentStatus={this.state.checked2} disabled />\n        <Switch currentStatus={this.state.checked3} disabled />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"滑动开关","__content":"\n\n<DEMO>\n\n| 参数          | 说明            | 类型    | 默认值    |\n| ------------- | --------------- | ------- | --------- |\n| prefixCls     | 组件 class 前缀 | string  | Yep-radio |\n| style         | 组件样式        | object  | {}        |\n| className     | 额外 class      | string  | -         |\n| currentStatus | 当前状态        | boolean | false     |\n| handleSwitch  | 切换回调        | func    | ()=>{}    |\n| disabled      | 是否禁用        | boolean | false     |\n"},"tab-bar":{"name":"tab-bar","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {TabBar, Icon} from '@jdcfe/yep-react';\nconst {Tab} = TabBar;\n\nclass Demo extends React.PureComponent {\n  state = {\n    index: 0,\n    hidden: false,\n  };\n\n  onClick = index => {\n    this.setState({\n      index,\n    });\n  };\n  render() {\n    return (\n      <div>\n        <div style={{backgroundColor: 'white', height: '100%', textAlign: 'center'}}>\n          <div style={{paddingTop: 60}}>Clicked {this.state.index + 1} tab， </div>\n          <a\n            style={{display: 'block', marginTop: 40, marginBottom: 20, color: '#ec3838'}}\n            onClick={e => {\n              e.preventDefault();\n              this.setState({\n                hidden: !this.state.hidden,\n              });\n            }}\n          >\n            Click to show/hide tab-bar\n          </a>\n        </div>\n\n        <TabBar hidden={this.state.hidden}>\n          <Tab\n            icon={<Icon type=\"dongdong\" />}\n            title=\"首页\"\n            dot\n            selectedIcon={<Icon type=\"dongdong\" color=\"#ec3838\" />}\n            selected={this.state.index === 0}\n            onClick={() => this.onClick(0)}\n          />\n          <Tab\n            icon={<Icon type=\"lego_yonghu\" />}\n            title=\"我的\"\n            badge={6}\n            selectedIcon={<Icon type=\"lego_yonghu\" color=\"#ec3838\" />}\n            selected={this.state.index === 1}\n            onClick={() => this.onClick(1)}\n          />\n          <Tab\n            icon=\"//placehold.it/24x24\"\n            title=\"他的\"\n            selectedIcon=\"//placehold.it/24x24\"\n            selected={this.state.index === 2}\n            onClick={() => this.onClick(2)}\n          />\n          <Tab\n            icon=\"//placehold.it/24x24\"\n            title=\"和的\"\n            selectedIcon=\"//placehold.it/24x24\"\n            selected={this.state.index === 3}\n            onClick={() => this.onClick(3)}\n          />\n        </TabBar>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"标签栏","__content":"\n\n位于 APP 底部，方便用户在不同功能模块之间进行快速切换。\n\n### 规则\n\n1.  用作 APP 的一级分类，数量控制在 3-5 个之间。\n2.  即使某个 Tab 不可用，也不要禁用或者移除该 Tab。\n\n<DEMO>\n\nTabBar\n\n| 参数         | 说明            | 类型   | 默认值      |\n| ------------ | --------------- | ------ | ----------- |\n| prefixCls    | 组件 class 前缀 | string | Yep-tab-bar |\n| style        | 组件样式        | object | {}          |\n| className    | 额外 class      | string | -           |\n| barTintColor | TabBar 背景色   | string | white       |\n| hidden       | 是否隐藏 TabBar | bool   | false       |\n\nTab\n\n| 参数                | 说明                                                | 类型            | 默认值          |\n| ------------------- | --------------------------------------------------- | --------------- | --------------- |\n| prefixCls           | 组件 class 前缀                                     | string          | Yep-tab-bar-tab |\n| style               | 组件样式                                            | object          | {}              |\n| className           | 额外 class                                          | string          | -               |\n| tintColor           | 选中状态颜色                                        | string          | #ec3838         |\n| unselectedTintColor | 未选中状态颜色                                      | string          | #888            |\n| selected            | 是否选中状态                                        | boolean         | false           |\n| icon                | 默认展示图片(图片的 url 地址或组件)                 | string 或 node  | -               |\n| selectedIcon        | 选中状态展示图片(图片的 url 地址或组件)             | string 或 node  | -               |\n| title               | 标题文字                                            | string          | -               |\n| badge               | 徽标数                                              | number \\ string | 无              |\n| dot                 | 是否在右上角显示小红点（在设置 badge 的情况下失效） | boolean         | false           |\n| onClick             | 点击事件回调                                        | func            | () => null      |\n"},"tabs":{"name":"tabs","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Tabs, WhiteSpace, Badge} from '@jdcfe/yep-react';\nconst {TabPanel} = Tabs;\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <div className=\"demo-title\">基本</div>\n        <Tabs\n          onChange={index => {\n            console.log(index);\n          }}\n          distanceToChangeTab={100}\n        >\n          <TabPanel tab=\"选项卡一\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡一内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡二\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡二内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡三\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡三内容\n            </div>\n          </TabPanel>\n        </Tabs>\n        <WhiteSpace />\n        <div className=\"demo-title\">基本</div>\n        <Tabs>\n          <TabPanel tab={<Badge text={'3'}>选项卡一</Badge>}>\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡一内容\n            </div>\n          </TabPanel>\n          <TabPanel tab={<Badge text={'今日(20)'}>选项卡二</Badge>}>\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡二内容\n            </div>\n          </TabPanel>\n          <TabPanel tab={<Badge dot>选项卡三</Badge>}>\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡三内容\n            </div>\n          </TabPanel>\n        </Tabs>\n        <WhiteSpace />\n        <div className=\"demo-title\">底部</div>\n        <Tabs tabBarPosition=\"bottom\">\n          <TabPanel tab=\"选项卡一\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡一内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡二\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡二内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡三\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡三内容\n            </div>\n          </TabPanel>\n        </Tabs>\n        <WhiteSpace />\n        <div className=\"demo-title\">无动画</div>\n        <Tabs animated={false}>\n          <TabPanel tab=\"选项卡一\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡一内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡二\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡二内容\n            </div>\n          </TabPanel>\n          <TabPanel tab=\"选项卡三\">\n            <div\n              style={{\n                height: 150,\n                display: 'flex',\n                justifyContent: 'center',\n                alignItems: 'center',\n                backgroundColor: '#fff',\n              }}\n            >\n              选项卡三内容\n            </div>\n          </TabPanel>\n        </Tabs>\n        <WhiteSpace />\n        <div className=\"demo-title\">垂直样式</div>\n        <div style={{height: 150}}>\n          <Tabs tabBarPosition=\"left\" tabDirection=\"vertical\">\n            <TabPanel tab=\"选项卡一\">\n              <div\n                style={{\n                  height: 200,\n                  display: 'flex',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  backgroundColor: '#fff',\n                }}\n              >\n                选项卡一内容\n              </div>\n            </TabPanel>\n            <TabPanel tab=\"选项卡二\">\n              <div\n                style={{\n                  height: 200,\n                  display: 'flex',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  backgroundColor: '#fff',\n                }}\n              >\n                选项卡二内容\n              </div>\n            </TabPanel>\n            <TabPanel tab=\"选项卡三\">\n              <div\n                style={{\n                  height: 200,\n                  display: 'flex',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  backgroundColor: '#fff',\n                }}\n              >\n                选项卡三内容\n              </div>\n            </TabPanel>\n          </Tabs>\n        </div>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"标签页","__content":"\n\n用于让用户在不同的视图中进行切换。\n\n<DEMO>\n\n### Tabs\n\n| 属性                    | 说明                       | 类型                                                | 默认值     | 必选  |\n| ----------------------- | -------------------------- | --------------------------------------------------- | ---------- | ----- |\n| prefixCls               | 样式前缀                   | string                                              | Yep-tabs   | false |\n| className               | 组件额外样式               | string                                              | -          | false |\n| tabBarPosition          | TabBar 位置                | 'top' 'bottom' 'left' 'right' top                   | false      |\n| renderTabBar            | 替换 TabBar                | ((props: TabBarPropsType) => React.ReactNode) false |            | false |\n| initialPage             | 初始化 Tab, index or key   | number string                                       |            | false |\n| page                    | 当前 Tab, index or key     | number string                                       |            | false |\n| swipeable               | 是否可以滑动内容切换       | boolean                                             | true       | false |\n| animated                | 是否开启切换动画           | boolean                                             | true       | false |\n| onChange                | tab 变化时触发             | (index: number) => void                             |            | false |\n| onTabClick              | tab 被点击的回调           | (tab: {title,index}), index: number) => void        |            | false |\n| usePaged                | 是否启用分页模式           | boolean                                             | true       | false |\n| tabDirection            | Tab 方向                   | 'horizontal' 'vertical'                             | horizontal | false |\n| tabBarUnderlineStyle    | tabBar 下划线样式          | React.CSSProperties any                             |            | false |\n| tabBarBackgroundColor   | tabBar 背景色              | string                                              |            | false |\n| tabBarActiveTextColor   | tabBar 激活 Tab 文字颜色   | string                                              |            | false |\n| tabBarInactiveTextColor | tabBar 非激活 Tab 文字颜色 | string                                              |            | false |\n| tabBarTextStyle         | tabBar 文字样式            | React.CSSProperties any                             |            | false |\n| renderTab               | 替换 TabBar 的 Tab         | (tab: Models.TabData) => React.ReactNode            |            | false |\n\n### Tabs.DefaultTabBar\n\n| 属性       | 说明               | 类型                                        | 默认值               | 必选  |\n| ---------- | ------------------ | ------------------------------------------- | -------------------- | ----- |\n| goToTab    | 跳转 Tab           | (index: number) => boolean                  |                      | true  |\n| tabs       | tab 数据           | []                                          |                      | true  |\n| activeTab  | 当前激活 Tab 索引  | number                                      |                      | true  |\n| animated   | 是否使用动画       | boolean                                     |                      | true  |\n| prefixCls  | 样式前缀           | string                                      | Yep-tabs-default-bar | false |\n| renderTab  | 替换 TabBar 的 Tab | (tab: {title,index}) => React.ReactNode     |                      | false |\n| page       | Tab 分页尺寸       | number                                      | 5                    | false |\n| onTabClick | tab 被点击的回调   | (tab: {title,index}, index: number) => void |                      | false |\n"},"to-top":{"name":"to-top","style":".totop-demo-p {\n    margin-bottom: 150px;\n}","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {ToTop} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <p className=\"totop-demo-p\">滚动至页面底部，查看右下角返回顶部按钮</p>\n        <ToTop className=\"my-totop\" onClick={e => console.log(e)} />\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"回到顶部","__content":"\n\n返回页面顶部\n\n<DEMO>\n\n| 参数          | 说明                         | 类型       | 默认值     |\n| ------------- | ---------------------------- | ---------- | ---------- |\n| prefixCls     | 组件 class 前缀              | string     | Yep-to-top |\n| style         | 组件样式                     | object     | {}         |\n| className     | 组件额外样式                 | string     | -          |\n| onClick       | 点击返回顶部时的回调函数     | func       | ()=>{}     |\n| smooth        | 是否使用平滑滚动             | boolean    | true       |\n| children      | 返回顶部按钮内容             | React.Node | -          |\n| displayHeight | 滚动高度超过该值时，渲染组件 | number     | 400        |\n| duration      | 滚动时长 `毫秒`              | number     | 1000       |\n"},"toast":{"name":"toast","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Toast, Button, WhiteSpace} from '@jdcfe/yep-react';\n\nclass Demo extends React.PureComponent {\n  render() {\n    return (\n      <div title=\"Radio\">\n        <Button\n          onClick={() => {\n            Toast.show('This is a toast tips !!!');\n          }}\n        >\n          显示\n        </Button>\n        <WhiteSpace />\n        <Button\n          onClick={() => {\n            Toast.fail('失败');\n          }}\n        >\n          显示 fail\n        </Button>\n        <WhiteSpace />\n        <Button\n          onClick={() => {\n            Toast.success('成功');\n          }}\n        >\n          显示 success\n        </Button>\n        <WhiteSpace />\n        <Button\n          onClick={() => {\n            Toast.loading('加载中...', 10);\n          }}\n        >\n          显示loading\n        </Button>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<Demo />, mountNode);\n```\n"}],"category":"Components","title":"轻提示","__content":"\n\n一种轻量级反馈/提示，可以用来显示不会打断用户操作的内容，适合用于页面转场、数据交互的等场景中。\n\n## 规则\n\n一次只显示一个 toast。有 Icon 的 Toast，字数为 4-6 个；没有 Icon 的 Toast，字数不宜超过 14 个。\n\n<DEMO>\n\nAPI\n\nToast.show(message, duration, onClose, mask)\n\nToast.success(message, duration, onClose, mask)\n\nToast.fail(message, duration, onClose, mask)\n\nToast.info(message, duration, onClose, mask)\n\nToast.loading(message, duration, onClose, mask)\n\n| 参数     | 说明                   | 类型   | 默认值  |\n| -------- | ---------------------- | ------ | ------- |\n| message  | 提示内容               | string | -       |\n| duration | 自动关闭的延时，单位秒 | number | 3       |\n| onClose  | 关闭后回调             | func   | () =>{} |\n\nduration: Toast.SHORT 3s Toast.LONG 8s\n"},"upload":{"name":"upload","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {Upload} from '@jdcfe/yep-react';\n\nclass UploadDemo extends React.PureComponent {\n  constructor() {\n    super();\n\n    this.state = {\n      files: [\n        {\n          url:\n            'https://img12.360buyimg.com/mobilecms/s140x140_jfs/t15502/344/609174093/288569/bf1a1744/5a377ae2N1fdb98cb.jpg',\n        },\n        {\n          url:\n            'https://img11.360buyimg.com/mobilecms/s140x140_jfs/t12055/16/2690023715/376068/6df77bbe/5a4b4fbbN31dcbd6c.jpg',\n        },\n        {\n          url:\n            'https://img14.360buyimg.com/mobilecms/s140x140_jfs/t16972/240/2218955674/343973/8b6a4c90/5aed4549N0a8b0eb0.jpg',\n        },\n        {url: 'http://git.jd.com/uploads/-/system/user/avatar/2143/avatar.png'},\n      ],\n    };\n\n    this.addImage = this.addImage.bind(this);\n    this.removeImage = this.removeImage.bind(this);\n  }\n\n  addImage(imgItem) {\n    this.setState({\n      files: this.state.files.concat([imgItem]),\n    });\n  }\n  removeImage(index) {\n    const {files} = this.state;\n    files.splice(index, 1);\n    this.setState({\n      files: [].concat(files),\n    });\n  }\n\n  render() {\n    const {files} = this.state;\n    return (\n      <div className=\"demo-list\">\n        <div className=\"demo-list-header\">基础upload组件</div>\n        <div className=\"demo-list-body\">\n          <Upload files={files} addImage={this.addImage} removeImage={this.removeImage} />\n        </div>\n      </div>\n    );\n  }\n}\n\nReactDOM.render(<UploadDemo />, mountNode);\n```\n"}],"category":"Components","title":"图片上传选择器","__content":"\n\n<DEMO>\n\n| 参数        | 说明                                     | 类型     | 默认值 |\n| ----------- | ---------------------------------------- | -------- | ------ |\n| files       | 图片对象数组，每个对象必须包含 url 属性  | array    | []     |\n| size        | 图片大小，需要全单位，比如\"80px\", \"2rem\" | string   | 75px   |\n| addImage    | 新增图片后的处理函数                     | function | ()=>{} |\n| removeImage | 删除图片后的处理函数                     | function | ()=>{} |\n"},"white-space":{"name":"white-space","demos":[{"order":0,"title":"基础用法","description":null,"__content":"\n\n```js\nimport React from 'react';\nimport {WhiteSpace} from '@jdcfe/yep-react';\n\nconst PlaceHolder = ({className = '', ...restProps}) => (\n  <div className={`${className} placeholder`} {...restProps}>\n    Block\n  </div>\n);\n\nconst WhiteSpaceExample = () => (\n  <div>\n    <WhiteSpace size=\"xs\" />\n    <PlaceHolder />\n\n    <WhiteSpace size=\"sm\" />\n    <PlaceHolder />\n\n    <WhiteSpace />\n    <PlaceHolder />\n\n    <WhiteSpace size=\"lg\" />\n    <PlaceHolder />\n\n    <WhiteSpace size=\"xl\" />\n    <PlaceHolder />\n  </div>\n);\n\nReactDOM.render(<WhiteSpaceExample />, mountNode);\n```\n"}],"style":".placeholder {\n  background-color: #ebebef;\n  color: #bbb;\n  text-align: center;\n  height: .6rem;\n  line-height: .6rem;\n  width: 100%;\n}\n","category":"Components","title":"上下留白","__content":"\n\n<DEMO>\n\n| 参数      | 说明                                | 类型   | 默认值         |\n| --------- | ----------------------------------- | ------ | -------------- |\n| prefixCls | 组件 class 前缀                     | string | Yep-whitespace |\n| style     | 组件样式                            | object | {}             |\n| className | 额外 class                          | string | -              |\n| size      | 上下留白的间距，可选 xs,sm,md,lg,xl | string | md             |\n| onClick   | 切换回调                            | func   | ()=>{}         |\n"}}}